<template>
  <div
    v-custom-scroll-bar
    class="content-selector"
  >
    <uc-grid
      :items="items"
      :columns="4"
    >
      <template v-slot="{ item }">
        <div class="content-selector__item">
          <content-selector-item
            :item="item"
            :show-name="!item.url"
            :selected="isSelected(item)"
            @navigateTo="navigateTo"
            @selectItem="selectItem"
          />
        </div>
      </template>
    </uc-grid>
  </div>
</template>

<script>
import ContentSelectorItem from './content-selector-item'
import UcGrid from '@/shared/components/generic/uc-grid'

function sortFolderFirst (a, b) {
  if (a.nodeType.toLowerCase() === 'folder') {
    return -1
  }
  if (b.nodeType.toLowerCase() === 'folder') {
    return 1
  }
  return 0
}

export default {
  name: 'ContentSelector',
  components: {
    ContentSelectorItem,
    UcGrid
  },
  props: {
    multiSelect: {
      type: Boolean,
      default: false
    },
    selectedItems: {
      type: Array,
      default: function () {
        return []
      }
    },
    currentNode: {
      type: Object,
      default: undefined
    }
  },
  computed: {
    items () {
      if (!this.currentNode.allElements) {
        return []
      }
      return [...this.currentNode.allElements].sort(sortFolderFirst)
    }
  },
  methods: {
    isSelected (item) {
      return this.selectedItems.includes(item.guid)
    },
    selectItem (item) {
      this.$emit('selected', item)
    },
    navigateTo (item) {
      this.$emit('navigateTo', item)
    }
  }
}
</script>

<style lang="stylus" scoped>
.content-selector__item
  margin-bottom 24px
</style>
