<template>
  <div
    class="uc-image-picker-grid-image"
    data-test="uc-image-picker-grid-image"
  >
    <uc-navigator-grid-leaf-node
      :is-selected="selected"
      :node="node"
      @select="selectImage"
    >
      <uc-image
        class="navigator-grid-image__image"
        :src="node.url"
        fit="scaleDown"
      >
        <template #loading>
          <loading-indicator :is-loading="true" />
        </template>
      </uc-image>
    </uc-navigator-grid-leaf-node>
  </div>
</template>

<script>
import UcNavigatorGridLeafNode from '@/shared/components/generic/uc-navigator/components/nodes/uc-navigator-grid-leaf-node'
import UcImage from '@/shared/components/generic/uc-image/uc-image'
import LoadingIndicator from '@/shared/components/widgets/loading-indicator'

const ComponentEvent = {
  OPEN: 'open',
  SELECT: 'select'
}

export default {
  name: 'GridImageComponent',
  components: {
    UcNavigatorGridLeafNode,
    UcImage,
    LoadingIndicator
  },
  props: {
    node: {
      type: Object,
      required: true
    },
    selected: {
      type: Boolean,
      default: false
    }
  },
  data  () {
    return {
      isHovered: false
    }
  },
  computed: {
    nodeUrl () {
      return this.node.url
    }
  },
  methods: {
    selectImage () {
      this.$emit(ComponentEvent.SELECT, this.node)
    }
  }
}
</script>
<style lang="stylus" scoped>

.navigator-grid-image__image
  width 100%
  max-height 100%
  border-radius 4px

</style>
