<template>
  <div class="product-details">
    <h2
      class="product-details__heading"
      data-test="product-details-heading"
    >
      {{ $tc('details.basicInformation') }}
    </h2>
    <section class="product-details__static">
      <div class="product-details__static-right">
        <uc-boolean
          :definition="details.displayOnWebsite"
          name="displayOnWebsite"
          @change="save"
        />
        <uc-short-text
          v-if="language"
          :language="language"
          :definition="details.displayName"
          name="displayName"
          @change="save"
        />
        <long-text
          v-if="language"
          :compact="false"
          :definition="details.shortDescription"
          :language="language"
          name="shortDescription"
          @change="save"
        />
        <uc-rich-text
          v-if="language"
          :definition="details.longDescription"
          :language="language"
          name="longDescription"
          @change="save"
        />
      </div>
    </section>
  </div>
</template>

<script>
import UcShortText from '../../../shared/components/editors/uc-short-text'
import LongText from '../../../shared/components/editors/uc-long-text'
import UcRichText from '../../../shared/components/editors/uc-rich-text'
import UcBoolean from '../../../shared/components/editors/uc-boolean'
export default {
  name: 'ProductDetails',
  components: {
    LongText,
    UcShortText,
    UcRichText,
    UcBoolean
  },
  props: {
    language: {
      type: String,
      default: ''
    },
    details: {
      type: Object,
      required: true
    }
  },
  consumedProperties: ['variantSku', 'key', 'sku', 'displayName', 'name', 'shortDescription', 'longDescription', 'displayOnWebsite', 'createdBy', 'createdOn', 'modifiedBy', 'modifiedOn', 'guid', 'primaryImage', 'thumbnailImage', 'definition', 'rating', 'productRelationType.Description', 'productRelationType.Name'],
  methods: {
    save (e) {
      this.$emit('change', e)
    }
  }
}
</script>

<style lang="stylus" scoped>
.product-details__heading
  primary-heading-2()
  margin 0 0 24px 0
  user-select none

.product-details__legacy-link
  float right
  font-size 16px

.product-details__static
  display flex
  align-items flex-start
  justify-content space-between

.product-details__static-right
  display grid
  grid-gap 32px
  grid-template-rows 3
  width: 100%

.primaryImage
  margin-bottom 25px

</style>
