import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import Axios from 'axios'
import truncateTooltip from '../shared/js/truncate-tooltip'
import { Format, locales } from '../shared/js/date-formatter'
import customScrollBar from '../shared/js/custom-scroll-bar'
import i18n from '../plugins/vue-i18n'

Vue.config.productionTip = false

// Setup Axios as http handler for Vue (including license interceptor)
const axiosInstance = Axios.create({
  baseURL: '/ucommerceapi/v2/'
})
axiosInstance.defaults.headers.post['Content-Type'] = 'application/json'
axiosInstance.defaults.headers.get['Content-Type'] = 'application/json'
Vue.prototype.axios = axiosInstance

Vue.filter('format', function (value, format, locale) {
  if (!value) return ''
  value = value.toString()
  return Format(value, format, locales[locale])
})

Vue.directive('truncate', truncateTooltip)
Vue.directive('custom-scroll-bar', customScrollBar)

new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app')
