<template>
  <image-picker-data-provider
    :content-type="contentType"
    @change="onChange"
  >
    <template v-slot="{ data, getData, isLoading, isEmptyRootState }">
      <div
        v-if="isEmptyRootState"
        class="uc-image-picker--multi-select__empty-root-state"
      >
        <uc-image-picker-empty-root-state :cms-host-name="hostSystemName" />
      </div>

      <!-- Image Picker -->
      <uc-image-picker
        v-else
        :nodes="data"
        :is-loading="isLoading"
        :is-empty-root-state="isEmptyRootState"
        :selected-nodes="definition.selected"
        :full-height="true"
        @getData="getData"
        @imageSelected="$emit('change', $event)"
      >
        <template
          #pending
        >
          <loading-indicator :is-loading="isLoading" />
        </template>

        <template
          v-slot:emptyContent="{ navigateBack }"
        >
          <div
            class="uc-image-picker--empty-node-state__container"
          >
            <h2 class="uc-image-picker--empty-node-state__title">
              {{ $t('global.emptyFolder') }}
            </h2>
            <div
              class="uc-image-picker--empty-node-state__back-container"
              @click="navigateBack"
            >
              <arrow-back />
              <span class="uc-image-picker--empty-node-state__back-label">{{ $tc('global.back') }}</span>
            </div>
          </div>
        </template>
      </uc-image-picker>
    </template>
  </image-picker-data-provider>
</template>

<script>

import UcImagePicker from '@/shared/components/generic/uc-image-picker/uc-image-picker'
import ImagePickerDataProvider from '@/shared/components/editors/uc-image-picker-editor/uc-image-picker/uc-image-picker--data-provider'
import LoadingIndicator from '@/shared/components/widgets/loading-indicator'
import ArrowBack from '@/shared/icons/16/arrow-back--16--normal'
import UcImagePickerEmptyRootState
  from '@/shared/components/editors/uc-image-picker-editor/uc-image-picker/components/uc-image-picker-empty-root-state'

import { mapGetters } from 'vuex'

export default {
  name: 'ImagePickerSingleSelect',
  components: {
    LoadingIndicator,
    ImagePickerDataProvider,
    UcImagePicker,
    ArrowBack,
    UcImagePickerEmptyRootState
  },
  props: {
    definition: {
      type: Object,
      required: true
    },
    contentType: {
      type: String,
      required: true
    },
    name: {
      type: String,
      required: true
    }
  },
  computed: {
    ...mapGetters(['systemInfo']),
    hostSystemName () {
      return this.systemInfo.cms
    }
  },
  methods: {
    onChange (saveCommand) {
      this.$emit('change', saveCommand)
    }
  }
}
</script>

<style lang="stylus" scoped>
/deep/.uc-image-picker--empty-node-state__container
  width 100%;
  text-align center

.uc-image-picker--empty-root-state__title, .uc-image-picker--empty-node-state__title
  primary-heading-2()

.uc-image-picker--empty-root-state__text
  secondary-body-text()
  margin-bottom 8px

.uc-image-picker--empty-node-state__back-label
  primary-details-text()
  margin-left 6px

.uc-image-picker--empty-node-state__back-container
  cursor pointer

.uc-image-picker--single-select__empty-root-state
  padding 0 120px
</style>
