<template>
  <uc-navigator
    :nodes="nodes"
    :is-loading="isLoading"
    :is-empty-root-state="isEmptyRootState"
    :navigation="navigation"
    @navigate="navigate"
  >
    <template
      #pending
    >
      <loading-indicator :is-loading="isLoading" />
    </template>
    <template
      #emptyContent
    >
      <div
        class="uc-image-picker__empty-state"
      >
        <h2 class="uc-image-picker__empty-state__title">
          {{ $t('global.emptyFolder') }}
        </h2>
        <div
          class="uc-image-picker__empty-state__back"
          @click="navigateBack"
        >
          <arrow-back />
          <span class="uc-image-picker__empty-state__back-label">{{ $t('global.back') }}</span>
        </div>
      </div>
    </template>
    <template #actions>
      <div class="uc-image-picker__actions">
        <slot name="actions" />
      </div>
    </template>
    <template #content>
      <div
        v-custom-scroll-bar
        class="uc-image-picker__container"
      >
        <!-- View -->
        <uc-navigator-view-grid-spacious
          :nodes="nodes"
        >
          <template v-slot="{item}">
            <!-- Node -->
            <component
              :is="getComponentByNodeType(item.pickerNodeType)"
              :node="item"
              :selected="isNodeSelected(item.id)"
              @select="onSelect"
              @open="onOpen"
            />
          </template>
        </uc-navigator-view-grid-spacious>
      </div>
    </template>
  </uc-navigator>
</template>

<script>
import LoadingIndicator from '@/shared/components/widgets/loading-indicator'
import { ArrowBack16Normal as ArrowBack } from '@/shared/icons'

import Constants from '@/shared/js/constants'
import UcImagePickerGridImage from '@/shared/components/editors/uc-image-picker-editor/uc-image-picker/components/uc-image-picker-grid-image'
import UcImagePickerGridFolder from '@/shared/components/editors/uc-image-picker-editor/uc-image-picker/components/uc-image-picker-grid-folder'

import {
  UcNavigator,
  UcNavigatorViewGridSpacious
} from '@/shared/components/generic/uc-navigator'

const ComponentEvent = {
  SELECT: 'imageSelected',
  GETDATA: 'getData'
}

const NodeTypeToComponentMap = {
  [Constants.NodeType.Image]: UcImagePickerGridImage,
  [Constants.NodeType.Folder]: UcImagePickerGridFolder
}

export default {
  name: 'UcImagePicker',
  components: {
    LoadingIndicator,
    ArrowBack,
    UcNavigator,
    UcNavigatorViewGridSpacious
  },
  props: {
    nodes: {
      type: Array,
      required: true
    },
    isPending: {
      type: Boolean,
      default: false
    },
    isEmptyRootState: {
      type: Boolean,
      default: false
    },
    selectedNodes: {
      type: Array,
      required: true
    },
    isLoading: {
      type: Boolean,
      default: () => false
    }
  },
  data () {
    return {
      navigation: []

    }
  },
  mounted () {
    this.navigation.push({
      name: 'Media',
      guid: -1,
      item: null
    })
  },
  methods: {
    isNodeSelected (nodeId) {
      return this.selectedNodes.includes(nodeId)
    },
    getComponentByNodeType (nodeType) {
      return NodeTypeToComponentMap[nodeType]
    },
    getData (node) {
      this.$emit(ComponentEvent.GETDATA, node)
    },
    navigate (node) {
      node.guid === -1 ? this.navigateToRootNode() : this.navigateToNode(node)
    },
    navigateBack () {
      if (this.navigation.length > 1) {
        this.navigate(this.navigation[this.navigation.length - 2])
      }
    },
    navigateToRootNode () {
      this.getData(null)
      this.navigation = [this.navigation[0]]
    },
    navigateToNode (node) {
      const indexOf = this.navigation.indexOf(node)
      this.getData(node.item)
      this.navigation.splice(indexOf + 1, this.navigation.length)
    },
    onSelect (node) {
      this.$emit(ComponentEvent.SELECT, node)
    },
    onOpen (node) {
      this.getData(node)
      this.navigation.push({
        name: node.name,
        guid: node.id,
        item: node
      })
    }
  }
}

</script>

<style scoped lang="stylus">
.uc-image-picker__empty-state
  width 100%;
  text-align center

.uc-image-picker__empty-state__title
  primary-heading-2()

.uc-image-picker__empty-state__back-label
  primary-details-text()
  margin-left 6px

.uc-image-picker__empty-state__back
  display flex
  align-items center;
  justify-content center
  cursor pointer

.uc-image-picker__view-switcher
  display flex
  justify-content flex-end
  padding-top 8px
  padding-bottom 8px

.uc-image-picker__container
  height 100%
  padding-right 120px
  padding-bottom: 128px
  padding-left 120px
  overflow auto

.uc-image-picker__actions
  display flex
  flex-direction column

.uc-image-picker__view-switcher__icon
  padding 4px
  cursor pointer
</style>
