<template>
  <button
    class="uc-button"
    :class="{ primary, floating, big, disabled, containingIcon, outlined, ...cssClassObject }"
    :disabled="disabled"
    :type="type"
    data-test="uc-button"
    @click="clicked"
  >
    <div
      v-if="!!this.$slots.before || centralSpacing"
      class="before-icon-wrapper"
    >
      <slot name="before" />
    </div>
    <span
      class="btn-text"
      data-test="btn-text"
      :class="{centered: centralSpacing}"
    > {{ text }} </span>
    <div
      v-if="!!this.$slots.after || centralSpacing"
      class="after-icon-wrapper"
    >
      <slot name="after" />
    </div>
  </button>
</template>

<script>
import { validateAgainstEnum } from '@/util/validateAgainst'

export const themeProp = {
  PRIMARY: 'primary',
  SECONDARY: 'secondary',
  WHITE: 'white',
  GHOST: 'ghost',
  TEXT: 'text'
}

export default {
  name: 'UcButton',
  props: {
    theme: {
      type: String,
      default: themeProp.PRIMARY,
      validator: validateAgainstEnum(themeProp)
    },
    text: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: 'button'
    },
    primary: {
      type: Boolean,
      default: false
    },
    floating: {
      type: Boolean,
      default: false
    },
    big: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    containingIcon: {
      type: Boolean,
      default: false
    },
    outlined: {
      type: Boolean,
      default: false
    },
    centralSpacing: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    cssClassObject () {
      return {
        'uc-button--theme-primary': this.theme === themeProp.PRIMARY,
        'uc-button--theme-secondary': this.theme === themeProp.SECONDARY,
        'uc-button--theme-ghost': this.theme === themeProp.GHOST,
        'uc-button--theme-white': this.theme === themeProp.WHITE,
        'uc-button--theme-text': this.theme === themeProp.TEXT
      }
    }
  },
  methods: {
    clicked (e) {
      this.$emit('click', e)
    }
  }
}
</script>

<style lang="stylus" scoped>
$height = 32px

.btn-text:only-child
  padding 0px 32px

.uc-button
  primary-details-text()
  display flex
  align-items center
  justify-content space-evenly
  max-width 200px
  height $height
  border none
  border-radius $height * 0.5
  background-color $white
  outline none
  cursor pointer
  transition background-color 150ms ease
  user-select none
  pointer-events auto

  &:hover
    text-decoration underline

  &.big
    height 48px
    border-radius 24px
    primary-body-text()

  &.primary
    color $white
    background-color $ucommerce-blue

    &:active:not(.disabled)
      box-shadow none
      transform scale(0.95)

    &:hover:not(.disabled)
      text-decoration none
      background-color lighten($ucommerce-blue, 8)

  &.floating
    box-shadow 0 4px 15px 0 rgba(0, 0, 0, 0.2)

  &.disabled
    text-decoration none
    background-color $input-field-stroke
    cursor default

  &.containingIcon
    display flex
    align-items center

    .btn-text
      padding 0 16px 0 12px

  &.outlined
    border solid 1px $input-field-stroke
    primary-details-text-semibold()
    border-radius 18px

.after-icon-wrapper,
.before-icon-wrapper
  display inline-flex

.centered
  padding 0 16px

.uc-button--theme-text
  height auto
  padding 0
  border-radius 0
  background-color transparent
</style>
