<template>
  <div
    class="language-selector"
    data-test="language-selector"
  >
    <v-popover
      :offset="18"
      :open="popoverOpen"
      width="200px"
      @hide="popoverOpen = false"
      @apply-show="popoverOpen = true"
    >
      <div
        v-if="!fitsSimpleEditor"
        class="pill-selected pill tooltip-target"
      >
        <span class="text">{{ contentLanguageName }}</span>
        <arrow-down class="arrow-down-icon" />
      </div>
      <template slot="popover">
        <div class="language-dropdown-container">
          <div
            v-for="language in languages"
            :key="language.value"
            :class="isSelectedLanguage(language) ? 'selected' : ''"
            class="language-option"
            data-test="language-option"
            @click="setContentLanguage(language.value); popoverOpen = false"
          >
            <span
              :title="language.text"
              class="text"
            >{{ language.text }}</span>
          </div>
        </div>
      </template>
    </v-popover>

    <div
      v-if="optionsCount > 0 && fitsSimpleEditor"
      ref="pillContainer"
      class="language-pill-container"
    >
      <div
        v-for="language in languages"
        :key="language.value"
        :class="isSelectedLanguage(language) ? 'pill-selected pill' : 'pill'"
        @click="setContentLanguage(language.value)"
      >
        <span
          :title="language.text"
          class="text"
        >{{ language.text }}</span>
      </div>
    </div>
  </div>
</template>

<script>
import ArrowDown from '../../../shared/icons/legacy/triangle-down-filled'
import { mapGetters, mapMutations } from 'vuex'

export default {
  name: 'LanguageSelector',
  components: {
    ArrowDown
  },
  data () {
    return {
      popoverOpen: false,
      fitsSimpleEditor: {
        type: Boolean,
        default: false
      }
    }
  },
  computed: {
    ...mapGetters(['languages', 'contentLanguage']),
    optionsCount () {
      return this.languages.length
    },
    contentLanguageName () {
      return this.languages.find(lang => lang.value === this.contentLanguage).text
    }
  },
  watch: {
    optionsCount: function () {
      this.setFitsSimpleEditor()
    }
  },
  mounted () {
    this.setFitsSimpleEditor()
  },
  methods: {
    ...mapMutations(['setContentLanguage']),
    setFitsSimpleEditor () {
      this.$nextTick(() => {
        if (this.$refs && this.$refs.pillContainer) {
          let elementsWidth = this.$refs.pillContainer.getBoundingClientRect().width
          this.fitsSimpleEditor = elementsWidth < 400
        }
      })
    },
    isSelectedLanguage (language) {
      return language.value === this.contentLanguage
    }
  }
}
</script>

<style lang="stylus" scoped>
.language-selector
  user-select none

.language-option,
.pill
  .text::after
    display block
    height 0
    overflow hidden
    font-weight bold
    visibility hidden
    content attr(title)

.text
  white-space nowrap

.language-pill-container
  display flex
  color $primary-text

.language-dropdown-container
  display flex
  flex-direction column
  height 100%
  color $primary-text

.pill
  display flex
  flex-grow 1
  align-items center
  justify-content space-between
  height 24px
  padding 2px 14px 2px 14px
  border-radius 13px
  cursor pointer
  primary-details-text()

.language-option
  display flex
  flex-grow 1
  align-items center
  justify-content space-between
  height 24px
  cursor pointer
  primary-details-text()

  &:hover
    primary-details-text-bold()

.language-option + .language-option
  margin-top 20px

.pill.pill-selected
  background-color $ucommerce-light-blue
  primary-details-text-bold()

.pill + .pill
  margin-left 17px

.arrow-down-icon
  width 12px
  height 13px
  margin-left 4px
  object-fit contain
  fill $primary-text
  stroke $primary-text

.selected
  primary-details-text-bold()

</style>
