<script>
export default {
  name: 'StepperWrapper',
  props: {
    steps: {
      type: Number,
      required: true
    }
  },
  data () {
    return {
      step: 1
    }
  },
  computed: {
    stepsArray () {
      return Array.from(Array(this.steps), (step, index) => index + 1)
    }
  },
  methods: {
    offset (offset) {
      const step = this.step + offset

      if (!this.stepsArray.includes(step)) {
        return
      }

      this.step = step
    },
    next () {
      this.offset(1)
      this.$emit('next', this.step)
    },
    previous () {
      this.offset(-1)
      this.$emit('previous', this.step)
    }
  },
  render () {
    return this.$scopedSlots.default({
      step: this.step,
      next: this.next,
      previous: this.previous
    })
  }
}
</script>
