<template>
  <div class="uc-image-picker-editor--multi-select">
    <editor-wrapper
      v-bind="{ compact, errors }"
      :required="isRequired"
    >
      <template #compactValue>
        <div
          v-if="imageSrc"
          class="compact"
        >
          <uc-image
            :readonly="isReadonly"
            :src="imageSrc"
            :alt="altText"
          />
        </div>
      </template>
      <div
        class="picker-container"
      >
        <div
          v-if="definition"
          class="stretch"
        >
          <section
            class="stretch media-area"
            data-test="multiPickerEditor"
            @mouseover="hovered = true"
            @mouseleave="hovered = false"
            @click.prevent="openModal"
          >
            <div
              v-show="!hasSelectedMedia && !isReadonly"
              class="stretch nothing-selected"
            >
              <div class="stretch nothing-selected inner">
                <uc-add-button v-bind="{ label }" />
              </div>
            </div>
            <uc-image
              v-if="hasSelectedMedia"
              class="stretch"
              :readonly="isReadonly"
              :src="imageSrc"
              :alt="altText"
              fit="scaleDown"
            />
            <label
              v-if="!imageSrc"
              class="name"
            >
              {{ altText }}
            </label>
            <template v-if="count > 0">
              <div
                v-if="hovered && (!isRequired || count > 1)"
                class="action action--remove"
              >
                <uc-button-round
                  v-if="!isRequired && hasSelectedMedia"
                  theme="white"
                  size="s"
                  floating
                  @click.stop="reset()"
                >
                  <remove-icon />
                </uc-button-round>
              </div>
              <div
                v-if="count > 1"
                class="meta"
              >
                {{ $t('ucImagePicker.additionalSelectedMediaCount', { count: count - 1 }) }}
              </div>
            </template>
          </section>
        </div>
        <uc-modal
          v-if="isModalOpen"
          size="lg"
          :title="$t('ucImagePicker.selectMediaHeader')"
          sticky-footer
          no-horizontal-padding
          no-bottom-padding
          @hide="closeModal"
        >
          <div class="uc-image-picker-editor__modal">
            <image-picker-data-provider
              ref="dataProvider"
              :content-type="contentType"
            >
              <template v-slot="{ data, getData, isLoading, isEmptyRootState }">
                <image-picker-multi-select
                  :data="data"
                  :get-data="getData"
                  :is-loading="isLoading"
                  :is-empty-root-state="isEmptyRootState"
                  :definition="definition"
                  :content-type="contentType"
                  @imagesSelected="imagesSelected"
                />
              </template>
            </image-picker-data-provider>
          </div>
        </uc-modal>
      </div>
    </editor-wrapper>
  </div>
</template>
<script>
import UcImage from '@/shared/components/generic/uc-image'
import UcModal from '@/shared/components/widgets/uc-modal'
import UcAddButton from '@/shared/components/forms/uc-button-add/uc-button-add'
import UcButtonRound from '@/shared/components/forms/uc-button-round'
import { Close16Normal as RemoveIcon } from '@/shared/icons'
import EditorWrapper from '@/shared/components/editors/editor-wrapper'
import editorMixin from '@//shared/components/editors/editor-mixin'
import createSaveCommand from '@/shared/js/create-save-command'
import ImagePickerDataProvider from '@/shared/components/editors/uc-image-picker-editor/uc-image-picker/uc-image-picker--data-provider'
import ImagePickerMultiSelect from '@/shared/components/editors/uc-image-picker-editor/uc-image-picker/uc-image-picker--multi-select'

export default {
  name: '',
  components: {
    ImagePickerMultiSelect,
    ImagePickerDataProvider,
    EditorWrapper,
    UcImage,
    UcModal,
    UcAddButton,
    UcButtonRound,
    RemoveIcon
  },
  mixins: [ editorMixin ],
  props: {
    definition: {
      type: Object,
      required: true
    },
    contentType: {
      type: String,
      default: 'media'
    },
    name: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      lastScrollPosition: Number,
      isModalOpen: false,
      hovered: false

    }
  },
  computed: {
    imageSrc () {
      return this.definition.mediaUrl
    },
    altText () {
      return this.definition.mediaName
    },
    count () {
      return this.selectedMediaGuids.length
    },
    confirmButtonLabel () {
      return `${this.$t('global.done')} (${this.count})`
    },
    hostSystemName () {
      return this.systemInfo.cms
    },
    selectedMediaGuids () {
      return this.definition.selected
    },
    hasSelectedMedia () {
      return this.definition.selected.length > 0
    }
  },
  methods: {
    closeModal () {
      this.isModalOpen = false
    },
    openModal () {
      this.isModalOpen = true
    },
    imagesSelected (selectedImages) {
      const saveCommand = createSaveCommand({
        definitionName: this.name,
        definitionGuid: this.definition.guid,
        label: this.definition.label,
        newValue: selectedImages.map(image => image.guid),
        oldValue: this.definition.selected || [],
        valueFieldName: 'selected',
        isNullable: !this.isRequired
      })

      if (selectedImages.length) {
        let { save } = saveCommand
        save[this.name].mediaName = selectedImages[0].name
        save[this.name].mediaUrl = selectedImages[0].url
      }
      let { undo } = saveCommand
      if (undo) {
        undo[this.name].mediaName = this.definition.mediaName
        undo[this.name].mediaUrl = this.definition.mediaUrl
        undo[this.name].mediaUrl = this.definition.mediaUrl
      }
      this.isModalOpen = false
      this.$emit('change', saveCommand)
    },
    reset () {
      this.imagesSelected([])
    }
  }
}
</script>

<style scoped lang="stylus">
$sizeSmall = 16px
$sizeBig = $sizeSmall * 2

.picker-container
  display: flex;
  flex-direction: column;
  margin-top: 16px;
  box(240px, 200px)

.compact
  box(50px)

.media-area
  position relative
  border-radius 4px
  background-image url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='4' ry='4' stroke='%23e8e8ed' stroke-width='2' stroke-dasharray='4%2c 5' stroke-dashoffset='22' stroke-linecap='round'/%3e%3c/svg%3e")

  .nothing-selected
    center()
    cursor pointer

    &.inner
      box(100%)
      font-weight 800
      font-size 4em

  .name
    cursor pointer

  .action
    position absolute
    padding 8px

  .action--remove
    top 0
    right 0

  .action--add
    right 0
    bottom 0

  .meta
    position absolute
    bottom 8px
    left 8px
    padding 4px 8px
    border-radius 12px
    primary-petit-text()
    background-color white
    user-select none
    pointer-events none

.stretch
  stretch()

.uc-image-picker-editor__modal
  position relative
  height 700px

.uc-image-picker-editor--multi-select
  cursor pointer
</style>
