<script>
import MapMedia from '@/catalogs/js/map-media-response'
import MapCats from '@/catalogs/js/map-category-response'

export default {
  name: 'ImagePickerDataProvider',
  components: {
  },
  props: {
    contentType: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      data: [],
      isLoading: false,
      isEmptyRootState: false,
      folderCount: undefined
    }
  },
  mounted () {
    this.getData(null)
  },
  methods: {
    async getData (node) {
      let endpoint = node ? `${this.contentType}/${node.nodeType}/${node.guid}` : `media/root`
      this.isLoading = true
      const result = await this.axios.get(endpoint)

      if (node === null && result.data.length === 0) {
        this.isEmptyRootState = true
        this.isLoading = false
        return
      }

      this.folderCount = +result.headers['x-total-count']
      const mappedResult = MapMedia(MapCats(result.data))
      this.data = this.sortArrayByNodeType(mappedResult)
      this.isLoading = false
    },
    sortArrayByNodeType (arr) {
      return arr.sort((a, b) => a.nodeType < b.nodeType ? -1 : (a.nodeType > b.nodeType ? 1 : 0))
    },
    setItems (items) {
      this.data = items
    }
  },
  render () {
    return this.$scopedSlots.default({
      data: this.data,
      getData: this.getData,
      isLoading: this.isLoading,
      isEmptyRootState: this.isEmptyRootState
    })
  }
}
</script>
