import * as types from './mutation-types'
import * as detailsTypes from './details/mutation-types'
import { nodeTypeApiMap } from '../js/tree-node-types'
import validate from '../js/validate'
import { mergeAllData } from '../js/merge-definitions-and-data'
const undoDelay = 20000
const errorMessageDelay = 10000

const setValidationMessage = (commit, save, errors) => {
  errors = errors || {}
  commit(`details/${detailsTypes.SET_VALIDATION_ERRORS}`, { save: save, errors: errors })
  if (Object.keys(errors).length > 0) {
    window.setTimeout(() => commit(`details/${detailsTypes.CLEAR_VALIDATION_ERRORS}`), errorMessageDelay)
  } else {
    commit(`details/${detailsTypes.CLEAR_VALIDATION_ERRORS}`)
  }
}

const actions = {
  async save ({ commit, state, rootState }, command) {
    const { save, meta } = command
    const errors = validate(state.details.details, save)

    if (errors) {
      return setValidationMessage(commit, save, errors)
    }

    try {
      const saveValue = Object.values(save)[0]
      let data = save
      let endpoint = `${nodeTypeApiMap[meta.nodeType]}/${meta.nodeGuid}/${meta.contentLanguage}`

      // If it is a price, it has a priceGroupGuid property
      if (saveValue && saveValue.hasOwnProperty('priceGroupGuid')) {
        endpoint = `${nodeTypeApiMap[meta.nodeType]}/${meta.nodeGuid}/prices`
        data = saveValue
      }

      const response = await this._vm.axios.patch(endpoint, data)

      if (save.hasOwnProperty('definition')) {
        const details = await this._vm.axios.get(`${nodeTypeApiMap[meta.nodeType]}/${meta.nodeGuid}/${meta.contentLanguage}`)
        // Due to the single product details endpoint getting back only data (not meta + data together like the Catalogs and Categories) we have to re-merge with the metadata.
        if (command.meta.nodeType === 'product') {
          const mergedObject = mergeAllData(rootState.definitionsByGuid, [details.data])
          commit(`details/${detailsTypes.SET_DETAILS}`, { details: mergedObject[0], shouldUpdateLastElement: true })
        } else {
          commit(`details/${detailsTypes.SET_DETAILS}`, { details: details.data, shouldUpdateLastElement: true })
        }
        await setValidationMessage(commit, save, response.data)
        return
      }

      await setValidationMessage(commit, save, response.data)
      commit(`details/${detailsTypes.UPDATE_DETAILS}`, save)
    } catch (error) {
      if (error.response && error.response.status === 400) {
        return setValidationMessage(commit, save, error.response.data)
      }

      throw error
    }

    commit(types.ADD_UNDO, command)
    window.setTimeout(() => {
      commit('removeUndo', command)
    }, undoDelay)
  },

  async moveCat ({ commit }, command) {
    const { save, undo, meta } = command
    try {
      const response = await this._vm.axios.patch(`${nodeTypeApiMap[meta.nodeType]}/${meta.nodeGuid}/${meta.contentLanguage}`, save)
      setValidationMessage(commit, save, response.data)
    } catch (error) {
      if (error.response && error.response.status === 400) {
        return setValidationMessage(commit, save, error.response.data)
      } else {
        throw error
      }
    }
    if (undo) {
      commit(types.ADD_UNDO, command)
      window.setTimeout(() => {
        commit('removeUndo', command)
      }, undoDelay)
    }
  },

  async undo ({ commit, state, rootState }, command) {
    const { undo, meta } = command
    commit(`details/${detailsTypes.UPDATE_DETAILS}`, undo)

    const errors = validate(state.details.details, undo)
    if (errors) {
      return setValidationMessage(commit, undo, errors)
    }

    commit(types.REMOVE_UNDO, command)

    try {
      const undoValue = Object.values(undo)[0]
      let data = undo
      let endpoint = `${nodeTypeApiMap[meta.nodeType]}/${meta.nodeGuid}/${meta.contentLanguage}`

      // If it is a price, it has a priceGroupGuid property
      if (undoValue && undoValue.hasOwnProperty('priceGroupGuid')) {
        endpoint = `${nodeTypeApiMap[meta.nodeType]}/${meta.nodeGuid}/prices`
        data = undoValue
      }

      const response = await this._vm.axios.patch(endpoint, data)

      if (undo.hasOwnProperty('definition')) {
        // If we're changing definition, we have to reload details
        const details = await this._vm.axios.get(`${nodeTypeApiMap[meta.nodeType]}/${meta.nodeGuid}/${meta.contentLanguage}`)
        // Due to the single product details endpoint getting back only data (not meta + data together like the Catalogs and Categories) we have to re-merge with the metadata.
        if (command.meta.nodeType === 'product') {
          const mergedObject = mergeAllData(rootState.definitionsByGuid, [details.data])
          commit(`details/${detailsTypes.SET_DETAILS}`, { details: mergedObject[0], shouldUpdateLastElement: true })
        } else {
          commit(`details/${detailsTypes.SET_DETAILS}`, { details: details.data, shouldUpdateLastElement: true })
        }
      }

      setValidationMessage(commit, undo, response.data)
    } catch (error) {
      if (error.response && error.response.status === 400) {
        return setValidationMessage(commit, undo, error.response.data)
      }

      throw error
    }

    if (meta.undoAction) {
      meta.undoAction()
    }
  },

  async delete ({ commit }, command) {
    const { undo, meta } = command
    if (undo) {
      commit(types.ADD_UNDO, command)
    }
    await this._vm.axios.delete(`${nodeTypeApiMap[meta.nodeType]}/${meta.nodeGuid}`)
  },

  setErrorMessage ({ commit }, message) {
    commit(types.SET_ERROR_MESSAGE, `Something went wrong on the web server. ${message}`)
    window.setTimeout(() => {
      commit(types.SET_ERROR_MESSAGE, null)
    }, errorMessageDelay)
  },

  setNotFoundMessage ({ commit }) {
    commit(types.SET_ERROR_MESSAGE, `A resource we were looking for was not found.`)
    window.setTimeout(() => {
      commit(types.SET_ERROR_MESSAGE, null)
    }, errorMessageDelay)
  }
}

export default actions
