<template>
  <div class="uc-image-picker">
    <editor-wrapper
      v-bind="{ compact, errors }"
      :required="isRequired"
    >
      <template #compactValue>
        <div
          v-if="imageSrc"
          class="compact"
        >
          <uc-image
            :readonly="isReadonly"
            :src="imageSrc"
            :alt="altText"
            fit="scale-down"
          />
        </div>
      </template>
      <div
        class="picker-container"
      >
        <div
          v-if="definition"
          class="stretch"
        >
          <section
            class="stretch media-area"
            @mouseover="hovered = true"
            @mouseleave="hovered = false"
            @click.prevent="toggle"
          >
            <div
              v-show="!hasSelectedMedia && !isReadonly"
              class="stretch nothing-selected"
            >
              <div class="stretch nothing-selected inner">
                <uc-button-add v-bind="{ label }" />
              </div>
            </div>
            <div class="media-area__image">
              <uc-image
                v-if="hasSelectedMedia"
                fit="scale-down"
                :readonly="isReadonly"
                :src="imageSrc"
                :alt="altText"
              />
            </div>
            <label
              v-if="!imageSrc"
              class="name"
            >
              {{ altText }}
            </label>
            <template v-if="count > 0">
              <div
                v-if="hovered && (!isRequired || count > 1)"
                class="action action--remove"
              >
                <uc-button-round
                  v-if="!multiSelect"
                  theme="white"
                  size="s"
                  floating
                  @click.stop="reset()"
                >
                  <remove-icon />
                </uc-button-round>
              </div>
              <div
                v-if="multiSelect"
                class="action action--add"
              >
                <uc-button-round
                  theme="primary"
                  size="s"
                  floating
                >
                  <add-icon />
                </uc-button-round>
              </div>
              <div
                v-if="count > 1"
                class="meta"
              >
                {{ $t('ucImagePicker.additionalSelectedMediaCount', { count: count - 1 }) }}
              </div>
            </template>
          </section>
        </div>
        <uc-modal
          v-if="toggled"
          size="lg"
          :title="hasData ? $t('ucImagePicker.selectMediaHeader') : ''"
          sticky-header
          sticky-footer
          @hide="toggle"
        >
          <div
            v-if="isLoading"
            class="loader"
          >
            <loading-indicator :is-loading="isLoading" />
          </div>
          <template v-if="hasData">
            <div
              v-if="multiSelect"
              class="selected-container"
            >
              <div
                v-show="!anySelected"
                class="popup-subtitle"
              >
                <p>{{ $t('ucImagePicker.noMediaSelected') }}</p>
              </div>
              <slider
                v-show="anySelected"
                :items="selectedMedia"
                :container-width="sliderContext.containerWidth"
                :item-size="sliderContext.itemSize"
                :item-margin="sliderContext.itemMargin"
              >
                <template #content="{ item }">
                  <div
                    class="slider-item"
                    :style="{
                      width: `${sliderContext.itemSize}px`,
                      height: `${sliderContext.itemSize}px`,
                      'margin-right': `${sliderContext.itemMargin}px`
                    }"
                    @click="toggleSelectedItems([item])"
                  >
                    <uc-image
                      class="slider-image"
                      fit="scale-down"
                      :src="item.url"
                      :alt="item.name"
                    />
                    <div class="deselect-slider-item">
                      <uc-button-round
                        theme="white"
                        size="s"
                        floating
                      >
                        <remove-icon />
                      </uc-button-round>
                    </div>
                  </div>
                </template>
              </slider>
            </div>
            <div class="breadcrumbs-container">
              <uc-breadcrumbs
                :items="breadcrumbs"
                @click="breadcrumbClick"
              />
              <div
                class="select-all-button"
                :class="{ 'is-visible': showButton }"
              >
                <uc-button
                  containing-icon
                  outlined
                  :text="areAllImagesInDirectorySelected ? unselectAllLabel : selectAllLabel"
                  @click="toggleSelectedItems(currentNodeImages)"
                >
                  <template #after>
                    <div class="select-all-button__icon">
                      <uc-icon-with-border
                        :icon="areAllImagesInDirectorySelected ? 'Dual16Normal' : 'Add16Normal'"
                      />
                    </div>
                  </template>
                </uc-button>
              </div>
            </div>
            <content-selector
              v-show="!isLoading"
              :multi-select="multiSelect"
              :selected-items="selectedMediaGuids"
              :current-node="currentNode"
              @selected="onSelected"
              @navigateTo="navigateTo"
            />
            <div
              v-if="currentTotalItems < 1 && !isLoading"
              class="empty-folder-container"
            >
              <h2 class="emptyFolderLabel">
                {{ $t('global.emptyFolder') }}
              </h2>
              <div
                class="back-container"
                @click="backClicked"
              >
                <arrow-back />
                <span class="back-label">{{ $tc('global.back') }}</span>
              </div>
            </div>
          </template>
          <div
            v-else-if="!isLoading"
            class="empty-state"
          >
            <h1 class="empty-state-title">
              {{ $t('ucImagePicker.noMediaTitle') }}
            </h1>
            <p class="empty-state-text">
              {{ $t('ucImagePicker.noMediaText', { name: hostSystemName }) }}
            </p>
          </div>
          <template
            #footer
          >
            <uc-button
              v-if="hasData && multiSelect"
              class="add-media-button"
              :text="confirmButtonLabel"
              :disabled="!anySelected && isRequired"
              primary
              big
              @click="saveAndClose"
            />
          </template>
        </uc-modal>
      </div>
    </editor-wrapper>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import MapCats from '@/catalogs/js/map-category-response'
import MapMedia from '@/catalogs/js/map-media-response'
import Constants from '@/shared/js/constants'
import UcImage from '@/shared/components/uc-image'
import ContentSelector from '@/catalogs/components/content-navigator/content-selector'
import createSaveCommand from '@/shared/js/create-save-command'
import UcModal from '../widgets/uc-modal'
import UcButton from '@/shared/components/forms/uc-button'
import UcBreadcrumbs from '@/shared/components/widgets/uc-breadcrumbs'
import ArrowBack from '@/shared/icons/16/arrow-back--16--normal'
import LoadingIndicator from '../widgets/loading-indicator'
import Slider from '../widgets/slider'
import UcIconWithBorder from '@/shared/components/widgets/uc-icon-with-border'
import UcButtonRound from '../forms/uc-button-round'
import UcButtonAdd from '../forms/uc-button-add'
import { Add16Normal as AddIcon, Close16Normal as RemoveIcon } from '@/shared/icons'
import editorMixin from './editor-mixin'

export default {
  name: 'UcImagePicker',
  components: {
    UcModal,
    UcImage,
    ContentSelector,
    UcButton,
    UcBreadcrumbs,
    ArrowBack,
    LoadingIndicator,
    Slider,
    UcIconWithBorder,
    UcButtonRound,
    UcButtonAdd,
    AddIcon,
    RemoveIcon
  },
  mixins: [ editorMixin ],
  props: {
    multiSelect: {
      type: Boolean,
      default: false
    },
    contentType: {
      type: String,
      default: 'media'
    },
    withLabel: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      toggled: false,
      hovered: false,
      lastScrollPosition: Number,
      currentTotalItems: Number,
      selectedMedia: [],
      areAllImagesInDirectorySelected: false,
      currentNode: undefined,
      emptyState: {
        type: Boolean,
        default: false
      },
      sliderContext: {
        containerWidth: 800,
        itemSize: 50,
        itemMargin: 16
      },
      breadcrumbs: {
        type: Array,
        default: () => { return [] }
      }
    }
  },
  computed: {
    ...mapGetters('contentSelector', ['isLoading']),
    ...mapGetters(['systemInfo']),
    imageSrc () {
      return this.definition.mediaUrl
    },
    altText () {
      return this.definition.mediaName
    },
    anySelected () {
      return this.count > 0
    },
    count () {
      return this.selectedMediaGuids.length
    },
    confirmButtonLabel () {
      return `${this.$t('global.done')} (${this.count})`
    },
    hostSystemName () {
      return this.systemInfo.cms
    },
    selectAllLabel () {
      return this.$t('global.selectAll')
    },
    unselectAllLabel () {
      return this.$t('global.unselectAll')
    },
    hasData () {
      return !this.emptyState
    },
    currentNodeImages () {
      return this.currentNode.allElements.filter(element => element.nodeType === Constants.ImagePicker.Image)
    },
    currentNodeImagesGuids () {
      return this.currentNodeImages.map(element => element.guid)
    },
    onlyFolders () {
      if (!this.currentNode.allElements) return false
      const folderCount = this.currentNode.allElements.filter(element => element.nodeType === Constants.ImagePicker.Folder).length
      const itemCount = this.currentNode.allElements.length
      return folderCount === itemCount
    },
    showButton () {
      return !(this.onlyFolders || !this.multiSelect)
    },
    selectedMediaGuids () {
      return this.selectedMedia.map(media => media.guid)
    },
    hasSelectedMedia () {
      return this.selectedMedia.length > 0
    }
  },
  watch: {
    toggled () {
      if (this.toggled) {
        this.lastScrollPosition = document.documentElement.scrollTop
        document.documentElement.style.overflow = 'hidden'
        document.addEventListener('keydown', this.keydownHandler)
      } else {
        document.documentElement.style.overflow = 'auto'
        document.removeEventListener('keydown', this.keydownHandler)
        document.documentElement.scrollTop = this.lastScrollPosition
      }
    },
    'definition.selected' () {
      this.initSelectedMediaUrls()
    }
  },
  mounted () {
    this.initSelectedMediaUrls()
  },
  methods: {
    ...mapMutations('contentSelector', ['toggleLoading']),
    async initSelectedMediaUrls () {
      if (this.compact) {
        return
      }

      if (!this.selected || this.selected.length === 0) {
        this.selectedMedia = []
        return
      }

      this.selectedMedia = this.selected.map(selectedItem => { return { guid: selectedItem } })
      let selectedMediaResult = await this.axios.post(`${this.contentType}`, this.selectedMediaGuids)
      this.selectedMedia = selectedMediaResult.data
    },
    onSelected (item) {
      this.selectItem(item)
      if (!this.multiSelect) {
        this.saveAndClose()
      }
    },
    toggle () {
      // Restore state from definition.selected when cancelling dialog (e.g. pressing ESC, clicking close button, clicking veil)
      if (this.toggled) {
        this.initSelectedMediaUrls()
      }

      this.breadcrumbs = []
      if (this.isReadonly) return
      this.toggled = !this.toggled
      this.initRootItems()
    },

    // --- Selection helpers
    selectItem (item) {
      const firstSelected = this.selectedMedia[0]
      if (!this.multiSelect && firstSelected) {
        this.toggleSelectedItems([firstSelected])
        if (firstSelected.guid === item.guid) {
          this.toggleSelectedItems([item])
        }
      }
      this.toggleSelectedItems([item])
    },

    setAreAllImagesInDirectorySelected () {
      const byAllImagesInDirectoryGuids = guid => this.currentNodeImagesGuids.includes(guid)
      this.areAllImagesInDirectorySelected = this.selectedMediaGuids.filter(byAllImagesInDirectoryGuids).length === this.currentNodeImages.length
    },
    // Returns items from `arr1` that are not in `arr2`
    difference (arr1, arr2) {
      return arr1.filter(item => !arr2.includes(item))
    },
    // This is useful for keeping selected items from other directories
    symmetricDifference (arr1, arr2) {
      const uniqueItems1 = this.difference(arr1, arr2)
      const uniqueItems2 = this.difference(arr2, arr1)
      return uniqueItems1.concat(uniqueItems2)
    },
    toggleSelectedItems (itemArray) {
      this.setAreAllImagesInDirectorySelected()
      if (this.areAllImagesInDirectorySelected && itemArray.length > 1) {
        this.toggleAllImagesInDirectory()
      } else {
        this.toggleSomeImagesInDirectory(itemArray)
      }
      this.setAreAllImagesInDirectorySelected()
    },
    toggleAllImagesInDirectory () {
      const difference = this.symmetricDifference(this.selectedMediaGuids, this.currentNodeImagesGuids)
      this.selectedMedia = this.selectedMedia.filter(media => difference.includes(media.guid))
    },
    toggleSomeImagesInDirectory (itemArray) {
      // filter elements from the current directory from selected data, so it allows replacement with the spread operator
      const itemsGuids = itemArray.map(item => item.guid)
      const otherSelectedItems = this.selectedMedia.filter(media => !itemsGuids.includes(media.guid))

      // items were already there therefore only `un-toggle` them
      const previouslySelectedItemsFromDirectory = this.selectedMediaGuids.filter(mediaGuid => itemsGuids.includes(mediaGuid))
      if (previouslySelectedItemsFromDirectory.length === itemArray.length) {
        this.selectedMedia = otherSelectedItems
      } else {
        this.selectedMedia = [...otherSelectedItems, ...itemArray]
      }
    },
    reset () {
      this.selectedMedia = []
      this.save([])
    },
    async save (guids) {
      const saveCommand = createSaveCommand({
        definitionName: this.name,
        definitionGuid: this.guid,
        label: this.label,
        newValue: guids,
        oldValue: this.selected || [],
        valueFieldName: 'selected',
        isNullable: !this.isRequired
      })

      if (guids.length) {
        const content = await this.axios.get(`${this.contentType}/${guids[0]}`)
        let { save } = saveCommand
        save[this.name].mediaName = content.data.name
        save[this.name].mediaUrl = content.data.url
      }
      let { undo } = saveCommand
      if (undo) {
        undo[this.name].mediaName = this.altText
        undo[this.name].mediaUrl = this.imageSrc
      }
      this.$emit('change', saveCommand)
    },
    saveAndClose () {
      this.save(this.selectedMediaGuids)
      this.toggle()
    },
    keydownHandler (e) {
      if (e.key === 'Escape') {
        this.toggle()
      }
    },
    navigateTo (node) {
      this.breadcrumbs.push({
        name: node.name,
        guid: node.guid,
        item: node
      })

      this.getItems(node)
    },
    async getRootItems (node) {
      const endpoint = `${this.contentType}/root`
      this.toggleLoading()
      const nodeData = await this.getData(endpoint, node)

      if (nodeData.allElements.length === 0) {
        this.emptyState = true
      } else {
        this.emptyState = false
        let mappedData = MapMedia([nodeData])[0]
        this.currentNode = mappedData

        this.breadcrumbs = []
        this.breadcrumbs.push({
          name: mappedData.name,
          guid: mappedData.guid,
          item: mappedData
        })
        this.setAreAllImagesInDirectorySelected()
      }
      this.toggleLoading()
    },
    async getItems (node) {
      this.toggleLoading()
      const endpoint = `${this.contentType}/${node.nodeType}/${node.guid}`
      const nodeData = await this.getData(endpoint, node)
      this.toggleLoading()
      this.currentNode = nodeData
      this.setAreAllImagesInDirectorySelected()
    },
    async getData (endpoint, node) {
      const result = await this.axios.get(endpoint)
      const mappedResult = MapMedia(MapCats(result.data))
      this.currentTotalItems = result.data.length
      return {
        ...node,
        elements: mappedResult.filter(element => element.pickerNodeType === Constants.ImagePicker.Folder),
        allElements: mappedResult.filter(element => element.pickerNodeType !== Constants.ImagePicker.File),
        categoriesCount: +result.headers['x-total-count']
      }
    },
    initRootItems () {
      const item = {
        name: this.$t('ucImagePicker.media'),
        hasChildren: true,
        nodeType: 'Folder',
        pickerNodeType: 'Folder',
        id: '-1'
      }
      this.getRootItems(item)
    },
    async breadcrumbClick (breadcrumb) {
      if (breadcrumb.guid === '-1') {
        this.initRootItems()
      } else {
        await this.getItems(breadcrumb.item)
      }
      const indexOf = this.breadcrumbs.indexOf(breadcrumb)
      this.breadcrumbs.splice(indexOf + 1, this.breadcrumbs.length)
    },
    backClicked () {
      this.breadcrumbClick(this.breadcrumbs[this.breadcrumbs.length - 2])
    }
  }
}
</script>

<style lang="stylus" scoped>
$sizeSmall = 16px
$sizeBig = $sizeSmall * 2

.picker-container
  display flex
  flex-direction column
  margin-top 16px
  box(240px, 200px)

.compact
  box(50px)

.media-area
  position relative
  display flex
  align-items center
  justify-content center
  border-radius 4px
  background-image url("data:image/svg+xml,%3csvg width='100%25' height='100%25' xmlns='http://www.w3.org/2000/svg'%3e%3crect width='100%25' height='100%25' fill='none' rx='4' ry='4' stroke='%23e8e8ed' stroke-width='2' stroke-dasharray='4%2c 5' stroke-dashoffset='22' stroke-linecap='round'/%3e%3c/svg%3e")

  .nothing-selected
    center()
    cursor pointer

    &.inner
      box(100%)
      font-weight 800
      font-size 4em

  .name
    cursor pointer

  .action
    position absolute
    padding 8px

  .action--remove
    top 0
    right 0

  .action--add
    right 0
    bottom 0

  .meta
    position absolute
    bottom 8px
    left 8px
    padding 4px 8px
    border-radius 12px
    background-color white
    user-select none
    pointer-events none
    primary-petit-text()

.media-area__image
  width 100%
  max-width calc(100% - 2px)
  height 100%
  max-height calc(100% - 2px)

.icon
  center()
  margin 4px
  padding 8px
  cursor pointer
  opacity 0.9

  &.label
    height 100%
    border-radius 24px

  &.small
    circle($sizeSmall)
    box($sizeBig)

  &.big
    circle($sizeBig)
    box($sizeBig * 2)

  &.edit
    margin-left auto

  &:hover
    background-color $col_light_gray

.icon-edit
  box($sizeBig)

.align-right
  margin-left auto

.stretch
  stretch()

.uc-image-picker__label
  display inline-block

.add-media-button
  bottom 30px

.breadcrumbs-container
  display flex
  align-items center
  justify-content space-between
  margin-bottom 16px

.empty-folder-container
  display flex
  flex-direction column
  align-items center
  align-self center
  width 100

  & .emptyFolderLabel
    primary-heading-2()
    margin-bottom 8px

  & .back-icon
    height 8px
    margin-bottom 1px
    transform rotate(180deg)

  & .back-label
    primary-details-text()
    margin-left 6px

.back-container
  cursor pointer

.loader
  position absolute
  top 50%
  left 50%
  align-self center
  margin auto
  transform translateX(-50%)

.selected-container
  position relative
  display flex
  align-items center
  margin 24px 0 16px

.popup-subtitle
  flex-shrink 0
  secondary-details-text()
  height 50px // Prevent layout from jumping when adding items to slider

.deselect-slider-item
  position absolute
  top 50%
  left 50%
  transform translate(-50%,-50%)
  visibility hidden

.slider-item
  position relative
  display inline-block
  cursor pointer

  &:hover .deselect-slider-item
    visibility visible

.slider-image
  box(100%)
  border solid 1px $col_border

.select-all-button
  flex-shrink 0
  visibility hidden

  &.is-visible
    visibility visible

.select-all-button__icon
  margin-right -3px

.empty-state
  width 300px
  margin auto
  text-align center

.empty-state-title
  primary-heading-2()

.empty-state-text
  secondary-body-text()
</style>
