<template>
  <section
    v-if="currentDetails && currentDetails.meta"
    :key="currentDetails.meta.entityGuid"
    class="details-view"
    data-test="generic-details"
  >
    <div class="details-header-placeholder">
      <header
        ref="headerContainer"
        class="details-header"
      >
        <div class="header-top">
          <div class="name-group">
            <h1
              v-truncate
              name="name"
              class="details-title"
              data-test="details-title"
            >
              {{ currentDetails.name.value }}
            </h1>
            <v-popover offset="18">
              <uc-button-round
                theme="ghost"
                size="m"
                data-test="generic-details-popover"
              >
                <i class="icon icon--truncate">
                  <truncate-icon />
                </i>
              </uc-button-round>
              <template slot="popover">
                <div class="actions-container">
                  <div
                    v-close-popover
                    class="action"
                    title="Rename"
                    data-test="generic-details-popover-rename"
                    @click="openDialog(currentDetails.name.guid)"
                  >
                    {{ $t('details.rename', {label: ''}) }}
                  </div>
                  <div
                    v-if="nodeType !== 'productVariant'"
                    v-close-popover
                    class="action"
                    title="Change definition"
                    data-test="generic-details-popover-definition"
                    @click="openDialog('definition')"
                  >
                    {{ $t('details.changeDefinition') }}
                  </div>
                  <div
                    v-if="nodeType === 'product'"
                    v-close-popover
                    class="action"
                    title="Change SKU"
                    data-test="generic-details-popover-sku"
                    @click="openDialog(currentDetails.sku.guid)"
                  >
                    {{ $t('details.changeSKU') }}
                  </div>
                  <div
                    v-if="nodeType === 'productVariant'"
                    v-close-popover
                    class="action"
                    title="Change variant SKU"
                    @click="openDialog(currentDetails.variantSku.guid)"
                  >
                    {{ $t('details.changeVariantSKU') }}
                  </div>
                  <div
                    v-close-popover
                    :title="deleteLabel"
                    class="action delete"
                    data-test="generic-details-popover-delete"
                    @click="openDialog('confirmDelete')"
                  >
                    {{ deleteLabel }}
                  </div>
                </div>
                <hr class="separator-line">
                <div class="audit-container">
                  <div class="audit-item">
                    <span class="audit-header">{{ $t('details.created') }}</span>
                    <br>
                    <span class="audit-text">{{ currentDetails.meta.createdOn | format('DD. MMM YYYY', uiLanguage) }} {{ $t('details.by') }} {{ currentDetails.meta.createdBy }}</span>
                  </div>
                  <div class="audit-item">
                    <span class="audit-header">{{ $t('details.modified') }}</span>
                    <br>
                    <span class="audit-text">{{ currentDetails.meta.modifiedOn | format('DD. MMM YYYY', uiLanguage) }} {{ $t('details.by') }} {{ currentDetails.meta.modifiedBy }}</span>
                  </div>
                </div>
              </template>
            </v-popover>
          </div>
          <language-selector v-if="shouldShowLanguageSelector" />
        </div>
        <div class="header-bottom">
          <div class="header-bottom-left">
            <p
              v-show="!isHeaderFixed"
              v-truncate
              class="details-subheader"
            >
              Definition: {{ definitionName }}
            </p>
            <p
              v-show="!isHeaderFixed"
              v-if="nodeType === 'product'"
              v-truncate
              class="details-subheader"
            >
              {{ currentDetails.sku.label }}: {{ currentDetails.sku.value }}
            </p>
            <p
              v-show="!isHeaderFixed"
              v-if="nodeType === 'productVariant'"
              v-truncate
              class="details-subheader"
            >
              {{ currentDetails.variantSku.label }}: {{ currentDetails.variantSku.value }}
            </p>
          </div>
        </div>
      </header>
    </div>
    <div class="details-content">
      <div
        v-if="currentDetails.media || currentDetails.primaryImage || dynamicProperties.length > 0"
        class="field-section-navigation"
      >
        <ul class="section-nav-ul">
          <li
            :class="{ active: isActiveNavField(nodeType) }"
            class="section-nav"
            @click="scrollToElement(nodeType, true)"
          >
            {{ $tc('details.basicInformation') }}
          </li>
          <li
            v-if="nodeType==='productCategory'"
            class="section-nav"
            :class="{ active: isActiveNavField('products') }"
            @click="scrollToElement('products', true)"
          >
            {{ $tc('details.products') }}
          </li>
          <li
            v-if="computedIsProductFamily"
            class="section-nav"
            :class="{ active: isActiveNavField('variants') }"
            @click="scrollToElement('variants', true)"
          >
            {{ $tc('details.variants') }}
          </li>
          <li
            v-if="nodeType === 'product' || nodeType === 'productVariant'"
            class="section-nav"
            :class="{ active: isActiveNavField('prices') }"
            @click="scrollToElement('prices', true)"
          >
            {{ $tc('details.prices') }}
          </li>
          <li
            v-if="nodeType === 'product' || nodeType === 'productVariant'"
            class="section-nav"
            :class="{ active: isActiveNavField('relations') }"
            @click="scrollToElement('relations', true)"
          >
            {{ $tc('details.relations') }}
          </li>
          <li
            v-if="mediaType"
            v-truncate
            :class="{ active: isActiveNavField(mediaType) }"
            class="section-nav"
            data-test="section-nav-media"
            @click="scrollToElement(mediaType, true)"
          >
            {{ $tc('details.media') }}
          </li>
          <li
            v-for="property in dynamicProperties"
            :key="currentDetails[property].guid"
            v-truncate
            :class="{ active: isActiveNavField(currentDetails[property].guid) }"
            class="section-nav"
            @click="scrollToElement(currentDetails[property].guid, true), setActiveNavField(currentDetails[property].guid)"
          >
            {{ currentDetails[property].label }}
          </li>
        </ul>
      </div>
      <div class="field-section-fields">
        <fields-section
          :ref="nodeType"
          :data-guid="nodeType"
          @sectionClicked="setActiveNavField(nodeType)"
        >
          <component
            :is="nodeTypeComponent"
            :language="contentLanguage"
            :details="currentDetails"
            @change="saveChange"
          />
        </fields-section>
        <!--Products-->
        <fields-section
          v-if="nodeType === 'productCategory'"
          ref="products"
          class="field-section-fields__list"
          data-guid="products"
          @sectionClicked="setActiveNavField('products')"
        >
          <product-list
            :content-language="contentLanguage"
            :category-guid="currentDetails.meta.entityGuid"
            :category-name="name"
            @initialLoadFinished="indexDomElements"
          />
        </fields-section>
        <!-- Products/variants -->
        <fields-section
          v-if="computedIsProductFamily"
          ref="variants"
          class="field-section-fields__list"
          data-guid="variants"
          @sectionClicked="setActiveNavField('variants')"
        >
          <variant-list
            :content-language="contentLanguage"
            :product-guid="currentDetails.meta.entityGuid"
            list-id="VariantsList"
            @initialLoadFinished="indexDomElements"
            @openVariantList="openVariantList"
          />
        </fields-section>
        <template v-if="priceEditors.length > 0">
          <fields-section
            v-if="nodeType === 'product' || nodeType === 'productVariant'"
            ref="prices"
            scrollable
            data-guid="prices"
            :heading="$tc('details.prices')"
            @sectionClicked="setActiveNavField('prices')"
          >
            <div
              v-for="(property, index) in priceEditors"
              :key="currentDetails[property].guid"
              :class="{ 'margin-bottom': index !== priceEditors.length - 1}"
            >
              <component
                :is="'price'"
                :language="contentLanguage"
                :definition="currentDetails[property]"
                :name="property"
                @change="saveChange"
              />
            </div>
          </fields-section>
        </template>
        <fields-section
          v-if="nodeType === 'product' || nodeType === 'productVariant'"
          ref="relations"
          class="field-section-fields__list"
          data-guid="relations"
          @sectionClicked="setActiveNavField('relations')"
        >
          <relations-list
            id="RelationsList"
            :language="contentLanguage"
            :product-guid="currentDetails.meta.entityGuid"
            @initialLoadFinished="indexDomElements"
          />
        </fields-section>
        <fields-section
          v-if="currentDetails.primaryImage"
          :ref="currentDetails.primaryImage.guid"
          :heading="$tc('details.media')"
          :data-guid="currentDetails.primaryImage.guid"
        >
          <div class="media-section">
            <component
              :is="currentDetails.primaryImage.editor"
              :with-label="true"
              :definition="currentDetails.primaryImage"
              :multi-select="false"
              name="primaryImage"
              content-type="media"
              @change="saveChange"
            />
            <component
              :is="currentDetails.thumbnailImage.editor"
              :with-label="true"
              :definition="currentDetails.thumbnailImage"
              :multi-select="false"
              name="thumbnailImage"
              content-type="media"
              @change="saveChange"
            />
          </div>
        </fields-section>
        <!--Catalogs-->
        <fields-section
          v-if="currentDetails.media"
          :ref="currentDetails.media.guid"
          :heading="$tc('details.media')"
          :data-guid="currentDetails.media.guid"
        >
          <div class="media-section align-left">
            <component
              :is="'media'"
              :with-label="true"
              :definition="currentDetails.media"
              :multi-select="false"
              name="media"
              content-type="media"
              @change="saveChange"
            />
          </div>
        </fields-section>
        <template v-if="dynamicProperties.length > 0">
          <fields-section
            v-for="property in dynamicProperties"
            :key="currentDetails[property].guid"
            :ref="currentDetails[property].guid"
            :heading="currentDetails[property].label"
            :data-guid="currentDetails[property].guid"
            @sectionClicked="setActiveNavField(currentDetails[property].guid)"
          >
            <component
              :is="currentDetails[property].editor"
              :language="contentLanguage"
              :definition="currentDetails[property]"
              :name="property"
              @change="saveChange"
            />
          </fields-section>
        </template>
      </div>
    </div>
    <property-editor-dialog
      v-if="dialogOpen === currentDetails.name.guid"
      :definition="currentDetails.name"
      :custom-header="nameEditorCustomHeader"
      name="name"
      @hideDialog="closeDialogs"
      @onInput="onInput"
    />
    <property-editor-dialog
      v-if="currentDetails.sku && dialogOpen === currentDetails.sku.guid"
      :definition="currentDetails.sku"
      name="sku"
      :custom-header="$t('details.changeSKU')"
      @hideDialog="closeDialogs"
      @onInput="onInput"
    />
    <property-editor-dialog
      v-if="dialogOpen === 'definition'"
      :definition="currentDetails.definition"
      name="definition"
      :custom-header="$t('details.changeDefinition')"
      @hideDialog="closeDialogs"
      @onInput="onInput"
    />
    <property-editor-dialog
      v-if="nodeType === 'productVariant' && dialogOpen === currentDetails.variantSku.guid"
      name="variantSku"
      :definition="currentDetails.variantSku"
      :custom-header="$t('details.changeVariantSKU')"
      @hideDialog="closeDialogs"
      @onInput="onInput"
    />
    <uc-warning-dialog
      v-if="dialogOpen === 'confirmDelete'"
      :title="deleteLabel"
      :text="deleteConfirmText"
      :proceed-button-text="$t('details.deleteConfirmProceed')"
      :cancel-button-text="$t('details.deleteConfirmCancel')"
      @proceed="deleteEntity"
      @cancel="closeDialogs"
    />
  </section>
</template>

<script>
/*
  *
  *  Displays details of and allows editing catalogs, categories, products, variants. Anything that complies with the shared data format
  *  It's main data source is the details store, and all editors rendered by generic-details will be bound to the properties of
  *  `this.currentDetails`. However, this control is agnostic of what specific-details component is rendered. It may be CatalogDetails, ProductDetails
  *  or some customer provided component.
  *
  */

import { nodeTypeSingular } from '../../js/tree-node-types'
import InfoWidget from './info-widget.vue'
import { mapGetters, mapActions, createNamespacedHelpers } from 'vuex'
import createSaveCommand from '../../../shared/js/create-save-command'
import Editors from '../../../shared/components/editors'
import SpecificDetails from './specific-details'
import ShortText from '../../../shared/components/editors/uc-short-text'
import FieldsSection from './fields-section'
import LanguageSelector from '@/catalogs/components/shared/language-selector'
import UcButtonRound from '../../../shared/components/forms/uc-button-round'
import { Truncate16Thick as TruncateIcon } from '../../../shared/icons'
import UcText from '../../../shared/components/forms/uc-text-input'
import PropertyEditorDialog from '@/catalogs/components/shared/property-editor-dialog'
import Simplebar from 'simplebar-vue'
import 'simplebar/dist/simplebar.min.css'
import UcWarningDialog from '../../../shared/components/widgets/uc-warning-dialog'
import VariantList from './variant-list'
import ProductList from './product-list'
import RelationsList from './relations-list'

const detailsStore = createNamespacedHelpers('details')
const builderStore = createNamespacedHelpers('builder')

export default {
  name: 'GenericDetails',
  components: {
    UcWarningDialog,
    InfoWidget,
    ShortText,
    FieldsSection,
    LanguageSelector,
    UcButtonRound,
    TruncateIcon,
    Simplebar,
    ...Editors,
    ...SpecificDetails,
    UcText,
    PropertyEditorDialog,
    VariantList,
    ProductList,
    RelationsList
  },
  data () {
    return {
      activeItem: this.nodeType,
      scrollPos: 0,
      domElements: [],
      // this offset is dynamically changing based on the scroll position -> it allows highlighting even bottom elements when user scrolls to the end of the page
      scrollOffset: 150,
      scrollFromMenu: { navScroll: false, sectionGuid: '' },
      isScrolling: null,
      // Amount of pixels from top of the scrolled element - This makes the top element to be 150px from the top line of the window
      initialScrollOffset: 150,
      initialHeaderTop: 0,
      isHeaderFixed: false,
      intersectionObserver: null,
      dialogOpen: {
        type: String,
        default: null
      },
      saveCommand: undefined
    }
  },
  computed: {
    ...detailsStore.mapGetters(['currentDetails', 'validationErrors']),
    ...mapGetters(['uiLanguage', 'contentLanguage', 'isProductFamily']),
    deleteLabel () {
      return this.$t('details.delete', { label: nodeTypeSingular[this.currentDetails.meta.nodeType] })
    },
    deleteConfirmText () {
      if (this.isDeletePermanent) {
        return this.$t(`details.deleteConfirmText_permanent`, { entity: `a ${nodeTypeSingular[this.currentDetails.meta.nodeType]}` })
      } else {
        return this.$t(`details.deleteConfirmText_undoable`, { entity: `a ${nodeTypeSingular[this.currentDetails.meta.nodeType]}` })
      }
    },
    isDeletePermanent () {
      return ['product', 'productVariant'].includes(this.nodeType)
    },
    nameEditorCustomHeader () {
      return this.$t('details.rename', { label: nodeTypeSingular[this.currentDetails.meta.nodeType] })
    },
    meta () {
      return this.currentDetails.meta ? this.currentDetails.meta : {}
    },
    name () {
      return this.currentDetails.name ? this.currentDetails.name.value : ''
    },
    definitionName () {
      if (this.currentDetails.meta.definition) {
        return this.currentDetails.meta.definition.name
      } else {
        return this.$t('details.noDefinition')
      }
    },
    nodeTypeComponent () {
      return SpecificDetails[this.currentDetails.meta.nodeType]
    },
    nodeType () {
      this.$nextTick(() => {
        this.indexDomElements()
        if (this.domElements.length === 1) {
          this.intersectionObserver.disconnect()
          this.detachScrollListeners()
        }
      }, 0)
      return this.currentDetails && this.currentDetails.meta ? this.currentDetails.meta.nodeType : null
    },
    anyValidationErrors () {
      return Object.getOwnPropertyNames(this.validationErrors || {}).filter(n => n !== '__ob__').length > 0
    },
    dynamicProperties () {
      const detailsComponent = SpecificDetails[this.currentDetails.meta.nodeType]
      if (!detailsComponent) return []
      const consumed = detailsComponent.consumedProperties.concat(['meta', 'sortOrder', 'name'])
      return Object.keys(this.currentDetails).filter(propertyName => !consumed.includes(propertyName) && this.currentDetails[propertyName].editor !== 'Price')
    },
    priceEditors () {
      return Object.keys(this.currentDetails).filter(propertyName => this.currentDetails[propertyName].editor === 'Price')
    },
    mediaType () {
      if (this.currentDetails.primaryImage) {
        return this.currentDetails.primaryImage.guid
      }
      if (this.currentDetails.media) {
        return this.currentDetails.media.guid
      }
      return undefined
    },
    shouldShowLanguageSelector () {
      if (this.currentDetails) {
        return this.currentDetails.meta.nodeType !== 'productVariant'
      }
      return false
    },
    computedIsProductFamily () {
      return this.isProductFamily
    }
  },
  watch: {
    contentLanguage: function () {
      if (this.currentDetails && this.currentDetails.meta) {
        this.changeContentLanguage(this.contentLanguage)
      }
    },
    currentDetails () {
      if (!this.currentDetails) {
        return
      }
      this.removeNavigationListeners()
      this.attachNavigationListeners()
    }
  },
  mounted () {
    // Used to reset scroll position in order to prevent opening details with scroll from previous page
    window.scroll(0, 0)
  },
  created () {
    this.attachNavigationListeners()
  },
  destroyed () {
    this.removeNavigationListeners()
  },
  methods: {
    ...detailsStore.mapActions(['changeDefinition', 'changeContentLanguage']),
    ...mapActions(['save', 'delete']),
    ...builderStore.mapMutations(['setCurrentNodeName']),
    async saveChange (command) {
      this.enrichCommand(command)
      await this.save(command)
    },
    async submitCommand () {
      await this.saveChange(this.saveCommand)
      if (this.currentDetails['name'].errors) {
        return
      }
      this.closeDialogs()
      if (this.saveCommand.save.hasOwnProperty('name')) {
        this.setCurrentNodeName(this.saveCommand.save.name.value)
      }
    },
    attachNavigationListeners () {
      this.$nextTick(() => {
        if (this.domElements.length > 0) {
          this.indexDomElements()
        }
      }, 0)
      window.addEventListener('scroll', this.handleHeader)
      this.attachScrollListeners()
    },
    removeNavigationListeners () {
      window.removeEventListener('scroll', this.handleHeader)
      this.detachScrollListeners()
      if (this.intersectionObserver) {
        this.intersectionObserver.disconnect()
      }
    },
    onInput (saveCommand) {
      this.saveCommand = saveCommand
    },
    closeDialogs () {
      this.dialogOpen = null
    },
    openDialog (guid) {
      this.dialogOpen = guid
    },
    handleHeader () {
      let header = this.$refs.headerContainer
      if (this.initialHeaderTop === 0) {
        this.initialHeaderTop = header.getBoundingClientRect().top
      }
      if (header.getBoundingClientRect().top <= 0 && window.scrollY > this.initialHeaderTop) {
        header.classList.add('fixed')
        this.isHeaderFixed = true
      } else {
        header.classList.remove('fixed')
        this.isHeaderFixed = false
      }
    },
    enrichCommand (command) {
      const { meta } = command
      meta.contentLanguage = this.contentLanguage
      meta.nodeType = this.currentDetails.meta.nodeType
      meta.nodeGuid = this.currentDetails.meta.entityGuid
    },
    async deleteEntity () {
      this.closeDialogs()
      const command = createSaveCommand({
        definitionName: 'deleted',
        definitionGuid: 'cbaf8c70-639c-4716-a891-00efb0fb82bc',
        label: 'Is Deleted',
        newValue: true,
        oldValue: false
      })
      const { meta } = command
      meta.contentLanguage = this.contentLanguage
      meta.nodeType = this.currentDetails.meta.nodeType
      meta.nodeGuid = this.currentDetails.meta.entityGuid
      meta.survivesNodeChange = true
      if (this.isDeletePermanent) {
        command.undo = undefined
      } else {
        meta.undoMessage = `${this.currentDetails.name.value} ${this.$t('global.wasDeleted')}`
        meta.undoAction = () => {
          this.$emit('resurrected', this.currentDetails.meta.entityGuid)
        }
      }
      await this.delete(command)
      this.$emit('deleted', { guid: meta.nodeGuid, isVariant: this.nodeType === 'productVariant' })
    },
    scrollToElement (refName, isFromMenu) {
      if (isFromMenu) {
        this.scrollFromMenu.navScroll = true
        this.scrollFromMenu.sectionGuid = refName
      }
      let domElement = this.$refs[refName]
      if (Array.isArray(domElement)) {
        domElement = domElement[0].$el
      } else {
        domElement = this.$refs[refName].$el
      }
      let offset = domElement.getBoundingClientRect().top + window.scrollY - this.initialScrollOffset
      // Check if its possible to scroll the page that much, if not adjust the offset to scroll as much as possible to highlight the proper item -> limit is the maximum scroll position if the scroll position is unreachable, just set active class
      const limit = Math.max(document.body.scrollHeight, document.body.offsetHeight, document.documentElement.clientHeight, document.documentElement.scrollHeight, document.documentElement.offsetHeight)
      if (limit - window.innerHeight < offset) {
        this.setActiveNavField(refName)
      }
      window.scrollTo({ top: offset, behavior: 'smooth' })
    },
    isActiveNavField (menuItem) {
      return this.activeItem === menuItem
    },
    setActiveNavField (menuItem) {
      if (menuItem !== '') {
        this.activeItem = menuItem
      }
    },
    // Method for highlighting the current active element in the navigation
    onScrollMenuHighlight () {
      const fieldSection = document.querySelectorAll('.details-content .field-section-fields')[0]

      if (fieldSection === undefined) return
      // Condition to check if there is need to modify offset for finding closest element
      if (fieldSection.getBoundingClientRect().height - window.scrollY < window.innerHeight &&
          !this.scrollFromMenu.navScroll) {
        const scrollBarHeight = window.innerHeight * (window.innerHeight / (fieldSection.getBoundingClientRect().height + fieldSection.offsetTop))
        this.scrollOffset = window.innerHeight - (fieldSection.getBoundingClientRect().height - window.scrollY - scrollBarHeight)
        if (window.scrollY < this.initialScrollOffset) {
          this.scrollOffset = this.initialScrollOffset
        }
        // Else reset the offset back to the initial value
      } else {
        this.scrollOffset = this.initialScrollOffset
      }

      this.scrollPos = window.scrollY + this.scrollOffset
      const visibleElements = this.domElements.filter(x => x.intersecting)
      const closestElIndex = this.findClosestDomElementIndex(this.scrollPos, visibleElements)
      const el = visibleElements[closestElIndex]

      if (!this.scrollFromMenu.navScroll) {
        this.setActiveNavField(el.guid)
      } else {
        this.setActiveNavField(this.scrollFromMenu.sectionGuid)
      }
    },
    // Method for finding the closest element based on the scroll position - used for loop because of the performance (reduce takes twice as much time)
    findClosestDomElementIndex (scrollPos, collection) {
      let closestValue = Infinity
      let closestIndex = -1
      for (let i = 0; i < collection.length; i++) {
        const diff = Math.abs(collection[i].topPos - scrollPos)
        if (diff < closestValue) {
          closestValue = diff
          closestIndex = i
        }
      }
      return closestIndex
    },
    // Method for saving all sections into data, therefore it is possible to work with the data afterwards (for example highlight current active nav item)
    indexDomElements () {
      this.$nextTick(() => {
        if (this.intersectionObserver) {
          this.intersectionObserver.disconnect()
        }
        let options = {
          root: null, // relative to document viewport
          rootMargin: '-50px', // margin around root. Values are similar to css property. Unitless values not allowed
          threshold: 0 // visible amount of item shown in relation to root
        }
        this.intersectionObserver = new IntersectionObserver(this.observeElements, options)
        this.setActiveNavField(this.nodeType)
        this.domElements = []
        const collection = document.querySelectorAll('.details-content .fields-section')
        for (let i = 0; i < collection.length; i++) {
          collection[i].setAttribute('data-pos', collection[i].getBoundingClientRect().top.toString())
          const guid = collection[i].getAttribute('data-guid')
          const topPos = collection[i].getBoundingClientRect().y
          this.domElements.push({ guid: guid, topPos: topPos, intersecting: false })
          this.intersectionObserver.observe(collection[i])
        }
        // Needed to have at least one highlighted element
        this.domElements[0].intersecting = true
        this.attachScrollListeners()
      })
    },
    observeElements (entries, observer) {
      for (let i = 0; i < entries.length; i++) {
        const domEL = this.domElements.find(x => x.guid === entries[i].target.getAttribute('data-guid'))
        if (domEL) {
          domEL.intersecting = entries[i].isIntersecting
        }
      }
    },
    attachScrollListeners () {
      // listener to auto-scroll the nav menu in case of many elements inside nav
      window.addEventListener('scroll', this.sectionNavAutoScroll)
      window.addEventListener('scroll', this.onScrollMenuHighlight)
    },
    detachScrollListeners () {
      window.removeEventListener('scroll', this.sectionNavAutoScroll)
      window.removeEventListener('scroll', this.onScrollMenuHighlight)
    },
    // Method for automatically scrolling the navbar in case there are that many properties that it requires vertical scrollbar
    sectionNavAutoScroll () {
      this.$nextTick(() => {
        window.clearTimeout(this.isScrolling)
        this.isScrolling = setTimeout(function () {
          this.scrollFromMenu.navScroll = false
          // Gets the active li and ul
          const activeElement = document.getElementsByClassName('active')[0]
          const sectionNavEl = document.querySelector('.section-nav-ul')

          if (typeof activeElement !== 'undefined' || typeof sectionNavEl !== 'undefined') {
          // Gets pixel position of active item
            const activeElOffset = activeElement.offsetTop
            // Gets last visible pixel
            const scrollPosition = sectionNavEl.scrollTop + sectionNavEl.clientHeight
            // Adding 10 pixel tolerance in case of the last element in the list
            const totalHeight = parseInt(window.getComputedStyle(activeElement).getPropertyValue('margin-top')) +
              parseInt(window.getComputedStyle(activeElement).getPropertyValue('margin-bottom')) +
              activeElement.clientHeight
            if (activeElOffset - scrollPosition > -totalHeight) {
            // ensures that the scroll always scrolls the element into visible part of the navScrollBar
              sectionNavEl.scrollTop = activeElOffset - sectionNavEl.clientHeight + totalHeight
            }
            if (activeElOffset < sectionNavEl.scrollTop) {
              sectionNavEl.scrollTop = activeElOffset
            }
          }
        // binds are necessary to preserve reactivity in event listener (the code is inside timeout)
        }.bind(this), 100)
      }, 0)
    },
    openVariantList (row) {
      window.scroll(0, 0)
      this.$emit('openVariantList', row)
      this.$nextTick(() => {
        this.indexDomElements()
      })
    }
  }
}
</script>

