<template functional>
  <div
    v-if="props.items.length > 0"
    class="uc-grid"
    :style="{ '--uc-grid--column-count': props.columns }"
  >
    <slot
      v-for="item in props.items"
      :item="item"
    />
  </div>
</template>

<script>
export default {
  name: 'UcGrid',
  props: {
    items: {
      type: Array,
      required: true
    },
    columns: {
      type: Number,
      default: 0
    }
  }
}
</script>

<style lang="stylus" scoped>
.uc-grid
  --uc-grid--column-count auto-fill
  --uc-grid--column-width minmax(0, 1fr)
  --uc-grid--column-gap 20px

  display grid
  grid-gap var(--uc-grid--column-gap)
  grid-template-columns repeat(var(--uc-grid--column-count), var(--uc-grid--column-width))

</style>
