<template>
  <media
    v-bind="{ definition, name, compact, language }"
    content-type="content"
    @change="onChange"
  />
</template>

<script>
import Media from './uc-image-picker'
import editorMixin from './editor-mixin'

export default {
  name: 'UcContent',
  components: {
    Media
  },
  mixins: [ editorMixin ],
  methods: {
    onChange (command) {
      this.$emit('change', command)
    }
  }
}
</script>
