<template>
  <div class="uc-navigator">
    <div
      v-if="isLoading"
      class="uc-navigator__pending"
    >
      <slot name="pending" />
    </div>
    <div class="uc-navigator__header">
      <div class="uc-navigator__header--inline">
        <uc-breadcrumbs
          :items="navigation"
          @click="navigate"
        />
        <slot name="actions" />
      </div>
    </div>
    <div v-if="nodes.length === 0 && !isEmptyRootState">
      <slot
        name="emptyContent"
      />
    </div>
    <div
      v-else
      class="uc-navigator__content"
    >
      <slot
        name="content"
      />
    </div>
  </div>
</template>

<script>

import ucBreadcrumbs from '@/shared/components/widgets/uc-breadcrumbs/uc-breadcrumbs'

export default {
  name: 'UcNavigator',
  components: {
    ucBreadcrumbs
  },
  props: {
    nodes: {
      type: Array,
      required: true
    },
    isLoading: {
      type: Boolean,
      default: false
    },
    navigation: {
      type: Array,
      required: true
    },
    isEmptyRootState: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    navigate (breadcrumb) {
      this.$emit('navigate', breadcrumb)
    }
  }
}

</script>

<style scoped lang="stylus">
.uc-navigator
  display flex
  flex-direction column
  height 100%

.uc-navigator__pending
  position absolute
  top 0
  right 0
  bottom 0
  left 0
  z-index 100
  display flex
  align-items center
  justify-content center
  background-color rgba(255, 255, 255, 1)

.uc-navigator__header
  height 48px
  padding 0 120px

.uc-navigator__header--inline
  display flex
  align-items center
  justify-content space-between

.uc-navigator__content
  flex 1 0 auto
  height 0
</style>
