<template>
  <div
    :class="{folder : isFolder}"
    class="content-selector-item"
    @mouseover="hovered = true"
    @mouseleave="hovered = false"
  >
    <uc-image
      :src="item.url"
      fit="scale-down"
      :alt="item.name"
      :is-container="isFolder"
      :child-count="item.childrenCount"
      v-on="{ click: isFolder ? navigateTo : selectItem }"
    />
    <div
      v-if="!isFolder && hovered && !selected"
      class="content-selector-item__icon"
      @click="selectItem"
    >
      <add-icon />
    </div>
    <div
      v-if="selected"
      class="content-selector-item__icon is-checked"
      @click="selectItem"
    >
      <checkmark-icon />
    </div>
    <p
      :class="{ folderLabel : isFolder }"
      class="content-selector-item__label"
      v-on="{ click: isFolder ? navigateTo : selectItem }"
    >
      {{ item.name }}
    </p>
  </div>
</template>

<script>
import UcImage from '../../../shared/components/uc-image'
import AddIcon from '../../../shared/icons/legacy/add'
import CheckmarkIcon from '../../../shared/icons/legacy/checkmark'

export default {
  name: 'ContentSelectorItem',
  components: {
    UcImage,
    AddIcon,
    CheckmarkIcon
  },
  props: {
    item: {
      type: Object,
      default: () => ({})
    },
    selected: {
      type: Boolean,
      default: false
    },
    showName: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      hovered: false,
      folderNodeTypeNames: ['folder', 'album', 'medialibrary', 'medialibraryfolder']
    }
  },
  computed: {
    // Sitefinity returns 'album' instead of 'folder'. Kentico returns 'mediaLibrary' instead of 'folder'. When we get rid of the old UIs we can unify them all returning 'folder'
    isFolder () {
      return this.folderNodeTypeNames.includes(this.item.nodeType.toLowerCase())
    }
  },
  methods: {
    navigateTo () {
      this.$emit('navigateTo', this.item)
    },
    selectItem () {
      this.$emit('selectItem', this.item)
    }
  }
}
</script>

<style lang="stylus" scoped>
.content-selector-item
  position relative
  width 100%
  height 150px
  border solid 1px $col_border
  border-radius 4px
  cursor pointer

  &.folder
    border none

.content-selector-item__label
  secondary-petit-text()
  margin 0
  padding 5px 0 0 0
  truncate()

  &.folderLabel
    primary-petit-text()
    padding 5px 0 0 0

.content-selector-item__icon
  position absolute
  right 10px
  bottom 10px
  display flex
  align-items center
  justify-content center
  border-radius 16px
  background #fff
  box-shadow 0 2px 6px 0 rgba(0, 0, 0, 0.15)
  box(24px)

  &.is-checked
    color $white
    background-color $ucommerce-blue
</style>
