<template>
  <div class="catalogs">
    <beta-banner>
      Welcome to the new and improved Ucommerce! We will keep expanding and adding new features and we hope you love it as much as we do.
      If you have any feedback, hit the button!
    </beta-banner>
    <div class="catalogs-inner">
      <section class="catalogs__builder-outer">
        <cat-builder ref="builder" />
      </section>
      <section class="catalogs__listing-outer">
        <div class="catalogs__listing-outer__right">
          <language-selector />
        </div>
        <fields-section class="catalogs__list-section">
          <list
            :id="listId"
            metadata-url="/products/definitions/full"
            :data-url="dataUrl"
            :default-columns-configuration="{
              'primaryImage': { 'width': '90px' },
              'sku': {},
              'name': { 'width': '250px' },
              'definition': {},
              'eur 15 pct': { 'width': '360px' }
            }"
            :header="$tc('list.products')"
            @sortBy="sortBy"
            @openEntityEditor="openEntityEditor"
          >
            <template #actionsHeader>
              <uc-button
                v-if="displayedNode.nodeType != 'productCatalog'"
                :text="$t('list.newButton')"
                primary
                @click="openDialog"
              />
            </template>
          </list>
          <create-product
            v-if="isCreateProductDialogOpen"
            :dialog-open="isCreateProductDialogOpen"
            :displayed-node="displayedNode"
            :content-language="contentLanguage"
            @hideDialog="closeDialog"
            @addProductToListAndOpenItsDetails="addProductToListAndOpenDetails"
            @productCreated="updateListCount"
          />
        </fields-section>
      </section>
      <undo
        :undos="undos"
        @undo="executeUndo"
      />
    </div>
    <overlay-modal
      v-if="hasDetails"
    >
      <template #header>
        <header
          class="products-header"
          @click="backButtonClicked"
        >
          <span
            class="products-header__back-link"
            data-test="products-back-button"
          >
            <arrow-back class="products-header__back-link__icon" />
            <span class="products-header__back-link__label">{{ $tc('global.back') }}</span>
          </span>

          <a
            class="products-header__legacy-link"
            :href="currentDetails.meta.productUrl"
            target="_self"
            title="For missing features, click here to edit in old version"
          >
            Edit in old version
          </a>
        </header>
      </template>
      <template #content>
        <generic-details
          @deleted="deleteEntityAndCloseDetails"
          @openVariantList="openEntityEditor"
        />
      </template>
    </overlay-modal>
  </div>
</template>

<script>
import CatBuilder from '@/catalogs/components/catalog-builder/catalog-builder.vue'
import LanguageSelector from '@/catalogs/components/shared/language-selector'
import List from '@/catalogs/components/generic-list/list'
import Undo from '../../shared/components/widgets/undo-widget'
import { nodeTypeApiMap } from '../js/tree-node-types'
import { createNamespacedHelpers, mapActions, mapGetters } from 'vuex'
import UcButton from '../../shared/components/forms/uc-button'
import CreateProduct from '../components/generic-list/create-product'
import GenericDetails from '@/catalogs/components/details-view/generic-details'
import OverlayModal from '../../shared/components/widgets/overlay-modal'
import ArrowBack from '../../shared/icons/16/arrow-back--16--normal'
import BetaBanner from '../../shared/components/widgets/beta-banner'
import setUrlParam from '../../shared/js/set-url-param'

import FieldsSection from '../components/details-view/fields-section'

const builderStore = createNamespacedHelpers('builder')
const genericDetailsStore = createNamespacedHelpers('genericList')
const detailsStore = createNamespacedHelpers('details')

export default {
  name: 'Products',
  components: {
    FieldsSection,
    CatBuilder,
    LanguageSelector,
    List,
    Undo,
    UcButton,
    CreateProduct,
    GenericDetails,
    OverlayModal,
    ArrowBack,
    BetaBanner
  },
  data () {
    return {
      dataUrl: '',
      isCreateProductDialogOpen: false,
      listId: 'Products'
    }
  },
  computed: {
    ...builderStore.mapGetters(['displayedNode']),
    ...genericDetailsStore.mapGetters(['lists']),
    ...detailsStore.mapGetters(['currentDetails']),

    ...mapGetters(['contentLanguage', 'undos', 'errorMessage', 'languages', 'contentLanguage']),
    currentList () {
      return this.lists[this.listId]
    },
    hasDetails () {
      return !!this.currentDetails
    }
  },
  watch: {
    displayedNode () {
      this.setUrl()
    },
    contentLanguage () {
      this.setUrl()
    }
  },
  methods: {
    ...detailsStore.mapMutations(['setDetails']),
    ...genericDetailsStore.mapMutations(['setListRows', 'removeItemsFromList', 'setCurrentListCount', 'addItemsToBeginningOfList']),
    ...mapActions(['undo']),
    setUrl () {
      if (this.displayedNode === undefined || this.displayedNode.nodeType === undefined) return
      this.dataUrl = this.getUrl()
    },
    getUrl (columnKey) {
      let baseUrl = `/${nodeTypeApiMap[this.displayedNode.nodeType]}/${this.displayedNode.guid}/products/${this.contentLanguage}`
      return setUrlParam(baseUrl, 'sort', columnKey)
    },
    executeUndo (command) {
      this.undo(command)
    },
    sortBy (columnKey) {
      this.dataUrl = this.getUrl(columnKey)
    },
    openDialog () {
      this.isCreateProductDialogOpen = true
      document.documentElement.style.overflow = 'hidden'
    },
    closeDialog () {
      this.isCreateProductDialogOpen = false
      document.documentElement.style.overflow = 'auto'
    },

    deleteEntityAndCloseDetails (payloadObject) {
      const listId = payloadObject.isVariant ? 'VariantsList' : this.listId
      this.removeItemsFromList({ listId, itemGuids: [ payloadObject.guid ] })
      this.openEntityEditor(null)
    },
    openEntityEditor (row) {
      this.setDetails({ details: row })
    },
    updateListCount () {
      this.setCurrentListCount({ id: this.listId, newCount: this.currentList.listCount + 1 })
    },
    addProductToListAndOpenDetails (row) {
      this.addItemsToBeginningOfList({ id: this.listId, newItems: [row] })
      this.openEntityEditor(row)
    },
    backButtonClicked () {
      window.scroll(0, 0)
      this.openEntityEditor(null)
    }
  }
}
</script>
<style lang="stylus" scoped>
.catalogs
  margin-bottom 30px

.catalogs__listing-outer__right
  display grid
  grid-column-gap 20px
  justify-items end
  margin-bottom 30px

.catalogs__list-section
  padding 0

.products-header
  position sticky
  top 8px
  z-index 1000
  display flex
  align-items center
  margin-top 20px
  margin-bottom 10px
  cursor pointer
  pointer-events all

.products-header__back-link,
.products-header__legacy-link
  display flex
  align-items center
  user-select none

.products-header__back-link__icon
  box(12px)
  transform rotate(180deg)

.products-header__back-link__label
  margin-left 6px
  primary-details-text()

.products-header__legacy-link
  margin-right 16px
  margin-left auto
  text-decoration none
  primary-details-text()

.catalogs__builder-outer
  min-height 230px
</style>
