<template>
  <div
    class="uc-rich-text"
    :class="cssClassObject"
    data-test="uc-rich-text"
  >
    <editor-wrapper
      v-bind="{ compact, errors }"
      :required="isRequired"
    >
      <template #compactValue>
        <!-- eslint-disable vue/no-v-html -->
        <span
          class="uc-rich-text__summary"
          v-html="value"
        />
      </template>
      <label v-if="label">{{ label }}</label>
      <div class="input-field-container">
        <div class="input-field-wrapper">
          <div ref="editor" />
        </div>
      </div>
    </editor-wrapper>
  </div>
</template>

<script>
import Quill from 'quill'
import editorMixin from './editor-mixin'

function isNullOrContentlessHTML (value) {
  return value === null ||
         value === undefined ||
         value === '' ||
         value === '<p><br></p>' ||
         value === '<pre class="ql-syntax" spellcheck="false">\n</pre>'
}

export default {
  name: 'UcRichText',
  mixins: [ editorMixin ],
  props: {
    theme: {
      type: String,
      default: 'snow'
    }
  },
  data () {
    return {
      editor: null,
      inputValue: '',
      condition: 'Idle'
    }
  },
  computed: {
    cssClassObject () {
      return {
        'is-readonly': this.isReadonly,
        'is-active': this.isActive,
        'is-blurred': !this.isActive,
        'has-content': this.hasInputValue
      }
    },
    isActive () {
      return this.condition === 'Active'
    },
    hasInputValue () {
      return !isNullOrContentlessHTML(this.inputValue)
    }
  },
  watch: {
    'definition.value': function () {
      if (this.compact) {
        return
      }

      this.syncWithDefinition()
    }
  },
  mounted () {
    if (this.compact) {
      return
    }

    // Setting up Quill Editor.
    this.editor = new Quill(this.$refs.editor, {
      modules: {
        toolbar: [
          [{ header: [1, 2, false] }],
          ['bold', 'italic', 'underline'],
          ['image', 'code-block']
        ]
      },
      theme: this.theme,
      readOnly: this.isReadonly,
      placeholder: ''
    })

    this.$nextTick(() => {
      this.editor.on('selection-change', this.onEditorSelectionChange)
      this.editor.on('text-change', this.onEditorTextChange)
      this.syncWithDefinition()
    })
  },
  methods: {
    syncWithDefinition () {
      this.editor.root.innerHTML = this.definition.value
      this.inputValue = this.definition.value
    },
    onEditorSelectionChange (event) {
      if (event === null) {
        this.onBlur()
      } else {
        this.onFocus()
      }
    },
    onEditorTextChange () {
      this.inputValue = this.editor.root.innerHTML
    },
    onFocus () {
      this.condition = 'Active'
    },
    onBlur () {
      const newValue = this.hasInputValue ? this.inputValue : null
      const oldValue = this.definition.value
      const isSameValue = oldValue === newValue || ((oldValue === undefined || oldValue === null || oldValue === '') && !this.hasInputValue)

      this.condition = this.hasInputValue ? 'Filled' : 'Idle'
      this.inputValue = newValue

      if (isSameValue && !this.hasErrors) {
        return
      }

      this.onChange({
        newValue,
        oldValue
      })
    }
  }
}
</script>
<!-- eslint-disable-next-line vue-scoped-css/require-scoped -->
<style lang="stylus">
@import "../../stylus/_vendor/quill/snow.styl"
@import "../../stylus/_vendor/quill/base.styl"
@import "../../stylus/_vendor/quill/core.styl"

.ql-toolbar.ql-snow
  border-bottom solid #e1e1e8 1px

// Content inserted with v-html cannot be targeted when style is scoped
.uc-rich-text__summary *
  primary-body-text()

/* stylelint-disable no-descending-specificity */
// Truncate and hide everything but first child in text in compact mode
.uc-rich-text__summary > *:first-child,
.uc-rich-text__summary > *:first-child > *:first-child
  margin 0
  padding 0
  overflow hidden
  white-space nowrap
  text-overflow ellipsis

.uc-rich-text__summary > *:first-child ~ *,
.uc-rich-text__summary > *:first-child  > *:first-child ~ *
  display none
</style>

<style lang="stylus" scoped>
.input-field-container
  border-radius 4px
  background-color #fff

  .input-field-wrapper
    padding 4px 16px 3px
    border 1px #e1e1e8 solid
    border-radius 4px
    background-color #fff

  &.disabled
    background-color $platform-bg

  &.danger
    background-color $col_red

.uc-rich-text
  position relative
  font-family $base_font_fam

  .ql-container.ql-snow
    padding-right 32px // Reserve space for feedback icon
    color $ucommerce-editors-active-input-color
    font-size 16px

  label
    position absolute
    top 56px
    left 14px
    z-index 9
    padding 0 2px 0 2px
    color $ucommerce-secondary
    font-size 16px
    line-height 30px
    background-color #fff
    transition-duration: 200ms
    user-select none
    pointer-events none

    .error
      color $col_red

    .ql-editor
      display block
      padding 12px 6px

      &.ql-blank::before
        left 6px

    .uc-rich-text .input-label
      display inline-block

    .uc-rich-text__tooltip
      margin-left 5px

  &.is-active,
  &.has-content
    label
      padding 0 4px
      font-size 12px
      line-height 20px
      background-color #fff
      transform translateY(-66px)

  &.is-readonly
    label
      background-color $platform-bg

    .input-field-container
      background-color $platform-bg

      .input-field-wrapper
        border 1px #e1e1e8 solid
        background-color $platform-bg

    .ql-container.ql-snow
      color #cacad7

  &.is-blurred
    .ql-container.ql-snow
      color $ucommerce-editors-idle-input-color
</style>
