<template>
  <div
    class="uc-image"
    :class="cssClassObject"
    @click="clickHandler"
  >
    <img
      v-if="!fallback && src"
      v-show="!hideImage"
      v-bind="{ src, alt }"
      @error="showDefault"
    >
    <camera-icon v-if="fallback" />
    <folder-icon
      v-if="isContainer || (!src && !fallback)"
    />
    <span
      v-if="isContainer"
      class="uc-image__counter"
    >
      {{ childCountLabel }}
    </span>
  </div>
</template>

<script>
import FolderIcon from '../icons/legacy/folder-image'
import CameraIcon from '../icons/legacy/camera'
import constants from '../../shared/js/constants'
import { validateAgainstEnum } from '@/util/validateAgainst'

const fitProp = {
  NONE: 'none',
  SCALE_DOWN: 'scale-down'
}

export default {
  name: 'UcImage',
  components: {
    FolderIcon,
    CameraIcon
  },
  props: {
    src: {
      type: String,
      default: ''
    },
    alt: {
      type: String,
      default: ''
    },
    readonly: {
      type: Boolean,
      default: false
    },
    isContainer: {
      type: Boolean,
      default: false
    },
    childCount: {
      type: Number,
      default: undefined
    },
    fit: {
      type: String,
      default: fitProp.NONE,
      validator: validateAgainstEnum(fitProp)
    }
  },
  data () {
    return {
      fallback: false
    }
  },
  computed: {
    cssClassObject () {
      return {
        'uc-image--fit-scale-down': this.fit === fitProp.SCALE_DOWN,
        'uc-image--readonly': this.readonly,
        'uc-image--shadow': !this.src && !this.fallback,
        'uc-image--folder': this.isContainer
      }
    },
    hideImage () {
      return this.src.includes(constants.UcImage.NotFound)
    },
    childCountLabel () {
      return this.$tc('contentNavigator.items', this.childCount)
    }
  },
  methods: {
    showDefault () {
      this.fallback = true
    },
    clickHandler () {
      this.$emit('click')
    }
  }
}
</script>

<style lang="stylus" scoped>
.uc-image
  display flex
  justify-content center
  cursor pointer

  img
    width auto
    max-width 100%
    height auto
    max-height 100%
    removeUserSelect()

  svg
    box(45%)

.uc-image--shadow
  box-shadow $default-box-shadow

.uc-image--readonly
  background-color transparent
  cursor default

.uc-image--fit-scale-down
  height 100%

  img
    object-fit scale-down

.uc-image--folder
  flex-direction column
  align-items center

.uc-image__counter
  input-field-disabled-text()
</style>
