<template>
  <div class="uc-wizard">
    <stepper-wrapper
      :steps="steps"
      @next="onChangeStep($event, 'next')"
      @previous="onChangeStep($event, 'previous')"
    >
      <template v-slot="{ step, next, previous }">
        <div class="uc-wizard__wrapper">
          <!-- Previous button -->
          <transition name="fade">
            <div
              v-show="step > 1"
              class="uc-wizard__previous"
            >
              <uc-button
                theme="text"
                :text="$tc('global.back')"
                data-test="previous"
                @click="previous()"
              >
                <template #before>
                  <arrow-back />
                </template>
              </uc-button>
            </div>
          </transition>

          <!-- Panels -->
          <div class="uc-wizard__container">
            <transition-group
              tag="div"
              :name="currentAnimation"
            >
              <div
                v-for="(stepSlotName, slotIndex) in stepSlotNames"
                v-show="slotIndex === (step - 1)"
                :key="stepSlotName"
                class="uc-wizard__panel"
                data-test="panel"
              >
                <slot :name="stepSlotName" />
              </div>
            </transition-group>
          </div>

          <!-- Next button -->
          <transition name="fade">
            <div
              v-show="step < steps"
              class="uc-wizard__next"
            >
              <uc-button
                big
                primary
                :disabled="disableNext"
                :text="$tc('global.continue')"
                data-test="next"
                @click="next()"
              />
            </div>
          </transition>
        </div>
      </template>
    </stepper-wrapper>
  </div>
</template>

<script>
import StepperWrapper from '@/shared/components/wrappers/stepper-wrapper'
import UcButton from '@/shared/components/forms/uc-button'
import { ArrowBack16Normal as ArrowBack } from '@/shared/icons'

export default {
  components: {
    StepperWrapper,
    UcButton,
    ArrowBack
  },
  props: {
    slotPrefix: {
      type: String,
      default: 'step-'
    },
    disableNext: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      currentAnimation: 'next'
    }
  },
  computed: {
    stepSlotNames () {
      return Object.keys(this.$scopedSlots).filter(slotName =>
        slotName.startsWith(this.slotPrefix)
      )
    },
    steps () {
      return this.stepSlotNames.length
    }
  },
  methods: {
    onChangeStep (step, direction) {
      this.currentAnimation = direction
      this.$emit('change', step)
    }
  }
}
</script>

<style lang="stylus" scoped>
.uc-wizard
  display flex
  flex-grow 1
  height 100%

.uc-wizard__wrapper
  position relative
  display flex
  flex-direction column
  flex-grow 1

.uc-wizard__container
  position relative
  display flex
  flex-grow 1
  height 100%

.uc-wizard__panel
  position absolute
  display flex
  flex-direction column
  align-items center
  justify-content space-between
  width 100%
  height 100%

.uc-wizard__previous
  position absolute
  top 0
  left 0
  z-index 1

.uc-wizard__next
  display flex
  flex-direction column
  align-items center
  justify-content center

.next-leave-active,
.next-enter-active,
.previous-leave-active,
.previous-enter-active
  transition all 500ms

.next-enter
  transform translate(100%, 0)

.next-leave-to
  transform translate(-100%, 0)

.previous-enter
  transform translate(-100%, 0)

.previous-leave-to
  transform translate(100%, 0)
</style>
