import * as types from './mutation-types'
import { nodeTypeApiMap } from '../../js/tree-node-types'
import { mergeAllData } from '../../js/merge-definitions-and-data'

const actions = {
  async changeContentLanguage ({ commit, state, rootState, getters }, contentLanguage) {
    // Language was changed, so we request product details for the product in the new contentLanguage.
    const nodeType = state.details[state.details.length - 1].meta.nodeType
    const entityGuid = state.details[state.details.length - 1].meta.entityGuid
    const details = await this._vm.axios.get(`${nodeTypeApiMap[nodeType]}/${entityGuid}/${contentLanguage}`)

    // Temporarily in place due to us only merging data + metadata in case of a product currently.
    if (nodeType !== 'productCatalog' && nodeType !== 'productCategory') {
      // Only overwrite what is new when language is changed, and keep common data e.g prices
      const mergedObject = mergeAllData(rootState.definitionsByGuid, [{ ...getters.currentDetails, ...details.data }])

      // Save the details to Vuex
      commit(types.SET_DETAILS, { details: mergedObject[0], shouldUpdateLastElement: true })
    } else {
      commit(types.SET_DETAILS, { details: details.data, shouldUpdateLastElement: true })
    }
  }
}

export default actions
