<template>
  <div class="slider">
    <div
      ref="sliderContainer"
      class="slider-container"
    >
      <div
        class="slider-track"
        :style="sliderTrackStyle"
      >
        <slot
          v-for="item in items"
          class="slider-item"
          name="content"
          :item="item"
        />
      </div>
    </div>
    <div
      v-if="hasMoreLeft"
      class="slider-gradient left"
    >
      <caret-round
        size="24px"
        rotate="180deg"
        @click="slideInDirection(-1)"
      />
    </div>
    <div
      v-if="hasMoreRight"
      class="slider-gradient right"
    >
      <caret-round
        size="24px"
        @click="slideInDirection(1)"
      />
    </div>
  </div>
</template>

<script>
import CaretRound from '../../icons/legacy/caret-round'

export default {
  name: 'Slider',
  components: {
    CaretRound
  },
  props: {
    items: {
      type: Array,
      default () {
        return []
      }
    },
    unit: {
      type: String,
      default: 'px'
    },
    containerWidth: {
      type: Number,
      required: true
    },
    itemSize: {
      type: Number,
      required: true
    },
    itemMargin: {
      type: Number,
      required: true
    }
  },
  data () {
    return {
      sectionCount: -1,
      sectionIndex: 0
    }
  },
  computed: {
    itemTotalWidth () {
      return this.itemSize + this.itemMargin
    },
    sectionItemCount () {
      return Math.floor(this.containerWidth / this.itemTotalWidth)
    },
    selectableItemsCount () {
      return this.sectionItemCount - 2
    },

    sliderContainerStyle () {
      return { width: `${this.containerWidth}${this.unit}` }
    },
    sliderTrackStyle () {
      return { width: `${this.sectionCount * this.containerWidth}${this.unit}` }
    },

    hasMoreLeft () {
      return this.sectionIndex > 0
    },
    hasMoreRight () {
      return this.sectionIndex < this.sectionCount - 1
    }
  },
  watch: {
    items () {
      this.updateSectionCount()
    }
  },
  mounted () {
    this.updateSectionCount()
  },
  methods: {
    calcSectionCount () {
      if (this.items.length <= this.sectionItemCount) {
        return 1
      }
      const count = Math.floor(this.items.length / this.selectableItemsCount)
      const remainder = this.items.length % this.selectableItemsCount
      return (remainder) === 0 ? count : count + 1
    },
    updateSectionCount () {
      this.sectionCount = this.calcSectionCount()
    },
    slideInDirection (direction) {
      const delta = this.itemTotalWidth * this.selectableItemsCount
      const slideBy = (delta * direction)
      this.$refs.sliderContainer.scrollLeft += slideBy
      this.sectionIndex += direction
    }
  }
}
</script>

<style lang="stylus" scoped>
$gradient_start = rgba(255, 255 255, 1)
$gradient_end = rgba(255, 255 255, 0)

.slider
  box(100%)
  position relative
  overflow hidden

.slider-container
  box(100%)
  overflow hidden
  scroll-behavior smooth

.slider-track
  display flex
  align-items center
  height 100%

.slider-gradient
  position absolute
  top 0
  z-index 1
  display flex
  align-items center
  height 100%

  &.left
    width 66px
    padding-left 5px
    background linear-gradient(to right, $gradient_start 0, $gradient_start 0.28, $gradient_end 1)

  &.right
    right 0
    justify-content flex-end
    width 100px
    padding-right 5px
    background linear-gradient(to left, $gradient_start 0, $gradient_start 0.28, $gradient_end 1)

  > div
    cursor pointer
</style>
