export default ({ definitionName, definitionGuid, label, newValue, oldValue, valueFieldName = 'value', isNullable, ...extra }) => {
  const undo = {
    undo: {
      [definitionName]: {
        guid: definitionGuid,
        [valueFieldName]: oldValue,
        ...extra
      }
    }
  }
  const meta = {
    meta: {
      label,
      contentLanguage: undefined,
      timeStamp: new Date(),
      nodeType: '',
      nodeGuid: '',
      undoMessage: undefined,
      undoAction: undefined
    }
  }
  const save = {
    save: {
      [definitionName]: {
        guid: definitionGuid,
        [valueFieldName]: newValue,
        ...extra
      }
    }
  }

  const hasOldValue = (oldValue !== null && oldValue !== undefined && !(Array.isArray(oldValue) && oldValue.length === 0))
  const isUndoable = isNullable || hasOldValue

  if (isUndoable) {
    return { key: definitionGuid, ...save, ...undo, ...meta }
  } else {
    return { key: definitionGuid, ...save, ...meta }
  }
}
