<template>
  <div
    class="category-details"
    data-test="category-details"
  >
    <h2 class="category-details__heading">
      {{ $tc('details.basicInformation') }}
    </h2>
    <section class="category-details__static">
      <div class="category-details__static-right">
        <uc-boolean
          :definition="details.displayOnWebsite"
          name="displayOnWebsite"
          @change="save"
        />
        <uc-short-text
          v-if="language"
          :language="language"
          :definition="details.displayName"
          name="displayName"
          @change="save"
        />
        <uc-rich-text
          v-if="language"
          :definition="details.description"
          :language="language"
          name="description"
          @change="save"
        />
      </div>
    </section>
  </div>
</template>

<script>
import UcShortText from '../../../shared/components/editors/uc-short-text'
import UcRichText from '../../../shared/components/editors/uc-rich-text'
import UcBoolean from '../../../shared/components/editors/uc-boolean'
import { mapGetters as globalGetters } from 'vuex'
export default {
  name: 'CategoryDetails',
  components: {
    UcShortText,
    UcRichText,
    UcBoolean
  },
  props: {
    language: {
      type: String,
      default: ''
    },
    details: {
      type: Object,
      required: true
    }
  },
  computed: {
    ...globalGetters(['systemInfo'])
  },
  consumedProperties: ['displayName', 'name', 'description', 'displayOnWebsite', 'media', 'definition'],
  methods: {
    save (e) {
      this.$emit('change', e)
    }
  }
}
</script>

<style lang="stylus" scoped>
.category-details__heading
  primary-heading-2()
  margin 0 0 24px 0
  user-select none

.category-details__static
  display flex
  align-items flex-start
  justify-content space-between

.category-details__static-right
  display grid
  grid-gap 32px
  grid-template-rows 3
  width 100%
</style>
