<template>
  <div
    v-if="stores.length > 0"
    class="store-selector"
  >
    <v-popover
      :offset="18"
      :open="popoverOpen"
      width="200px"
      @hide="popoverOpen = false"
      @apply-show="popoverOpen = true"
    >
      <div class="pill-selected pill tooltip-target">
        <span class="text">{{ selectedStoreLabel }}</span>
        <arrow-down class="arrow-down-icon" />
      </div>
      <template slot="popover">
        <div class="store-dropdown-container">
          <div
            v-for="store in stores"
            :key="store.value"
            class="store-option"
            @click="setSelectedStoreGuid(store); popoverOpen = false"
          >
            <span
              :title="store.label"
              class="text"
            >{{ store.label }}</span>
          </div>
        </div>
      </template>
    </v-popover>
  </div>
</template>

<script>
import MapStores from '../../js/map-get-stores-response'
import { createNamespacedHelpers } from 'vuex'
import ArrowDown from '../../../shared/icons/legacy/triangle-down-filled'
const builderStore = createNamespacedHelpers('builder')
const persistedStore = createNamespacedHelpers('persisted')

export default {
  name: 'StoreSelector',
  components: {
    ArrowDown
  },
  data () {
    return {
      popoverOpen: false
    }
  },
  computed: {
    ...builderStore.mapGetters(['stores']),
    ...persistedStore.mapGetters(['selectedStoreGuid']),
    selectedStoreLabel () {
      if (this.selectedStore) {
        return this.selectedStore.label
      } else {
        return this.$t('global.selectNoOptionsPlaceholder')
      }
    },
    selectedStore () {
      return this.stores.find(x => x.guid === this.selectedStoreGuid)
    }
  },
  created () {
    this.getStores()
  },
  methods: {
    ...builderStore.mapMutations(['setStores']),
    ...persistedStore.mapMutations(['setSelectedStoreGuid']),
    async getStores () {
      const res = await this.axios.get('/stores')
      const mappedStores = MapStores(res.data)
      this.setStores(mappedStores)
      if (!this.stores.length) return
      if (!this.selectedStore) {
        this.setSelectedStoreGuid(this.stores[0])
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.store-selector
  margin-bottom 12px
  user-select none

.store-option .text::after
  display block
  height 0
  overflow hidden
  font-weight bold
  visibility hidden
  content attr(title)

.store-dropdown-container
  display flex
  flex-direction column
  height 100%
  color $primary-text

.pill
  display flex
  flex-grow 1
  align-items center
  justify-content space-between
  height 24px
  padding 2px 14px 2px 14px
  border-radius 13px
  cursor pointer
  primary-details-text()

.store-option
  display flex
  flex-grow 1
  align-items center
  justify-content space-between
  height 24px
  cursor pointer
  primary-details-text()

  &:hover
    primary-details-text-bold()

.store-option + .store-option
  margin-top 20px

.pill.pill-selected
  background-color $ucommerce-light-blue
  primary-details-text-bold()

.pill + .pill
  margin-left 17px

.arrow-down-icon
  width 12px
  height 13px
  margin-left 4px
  object-fit contain
  fill $primary-text
  stroke $primary-text

.selected
  primary-details-text-bold()

.single-store-selector
  display inline-block
  max-width 200px
  cursor default
</style>
