<template>
  <div
    class="catalog-details"
    data-test="catalog-details"
  >
    <h2 class="catalog-details__header">
      Basic Information
    </h2>
    <section class="catalog-details__static">
      <div class="catalog-details__static-right">
        <uc-boolean
          :definition="details.displayOnWebsite"
          name="displayOnWebsite"
          @change="save"
        />
        <uc-boolean
          :definition="details.showPricesIncludingVAT"
          name="showPricesIncludingVAT"
          @change="save"
        />
        <uc-short-text
          v-if="language"
          :language="language"
          :definition="details.displayName"
          name="displayName"
          @change="save"
        />
        <uc-single-select-editor
          name="defaultPriceGroup"
          :definition="details.defaultPriceGroup"
          @change="save"
        />
        <uc-multi-select-editor
          name="allowedPriceGroups"
          :definition="details.allowedPriceGroups"
          @change="save"
        />
      </div>
    </section>
  </div>
</template>

<script>
import UcSingleSelectEditor from '../../../shared/components/editors/uc-single-select-editor'
import UcShortText from '../../../shared/components/editors/uc-short-text'
import UcBoolean from '../../../shared/components/editors/uc-boolean'
import UcMultiSelectEditor from '../../../shared/components/editors/uc-multi-select-editor'
import { mapGetters as globalGetters } from 'vuex'
export default {
  name: 'CatalogDetails',
  components: {
    UcSingleSelectEditor,
    UcMultiSelectEditor,
    UcShortText,
    UcBoolean
  },
  props: {
    language: {
      type: String,
      default: ''
    },
    details: {
      type: Object,
      required: true
    }
  },
  computed: {
    ...globalGetters(['systemInfo'])
  },
  consumedProperties: ['displayName', 'name', 'displayOnWebsite', 'showPricesIncludingVAT', 'defaultPriceGroup', 'allowedPriceGroups', 'definition'],
  methods: {
    save (e) {
      this.$emit('change', e)
    }
  }
}

</script>

<style lang="stylus" scoped>
.catalog-details__header
  primary-heading-2()
  margin 0 0 24px 0
  user-select none

.catalog-details__static
  display flex
  align-items flex-start
  justify-content space-between

.catalog-details__static-right
  display grid
  grid-gap 32px
  grid-template-rows 3
  width 100%

.compact
  margin-bottom 0
</style>
