export const toggleObjectInArray = (array, value) => {
  if (value && array) {
    const index = array.findIndex(node => node.guid === value.guid)
    if (index === -1) {
      array.push(value)
    } else {
      array.splice(index, 1)
    }
  }
}
export const addObjectToArray = (array, value) => {
  if (value && array) {
    const index = array.findIndex(node => node.guid === value.guid)
    if (index === -1) {
      array.push(value)
    }
  }
}
export const removeObjectFromArray = (array, value) => {
  if (value && array) {
    const index = array.findIndex(node => node.guid === value.guid)
    if (index !== -1) {
      array.splice(index, 1)
    }
  }
}
