<template>
  <transition name="dropdown">
    <div
      v-if="shown"
      class="container"
    >
      <a class="beta">BETA</a>
      <div class="message">
        <slot />
      </div>
      <a
        class="feedback"
        href="mailto:feedback@ucommerce.net"
        target="_blank"
      >Feedback</a>
      <button
        class="close"
        @click="hide"
      >
        ×
      </button>
    </div>
  </transition>
</template>

<script>
import { localStore } from '../../js/storage'
export default {
  name: 'BetaBanner',
  data () {
    let hidden = localStore.get('hideBetaBanner')
    return {
      shown: !hidden
    }
  },
  methods: {
    hide () {
      localStore.set('hideBetaBanner', true)
      this.shown = false
    }
  }
}
</script>

<style lang="stylus" scoped>
.container
  display grid
  grid-template-columns 120px auto 150px 108px
  justify-items start
  width 100%
  height 80px
  padding 22px 0
  color #fff
  font-size 12px
  background-color $ucommerce-blue
  place-items center

  .beta
    justify-self center
    font-weight bold
    font-size 20px

  .message
    max-height 36px
    margin-top -20px
    line-height 1.33
    white-space pre-line

  .feedback
    display flex
    align-items center
    justify-content center
    width 101px
    height 34px
    border solid 1px rgba(255, 255, 255, 0.3)
    border-radius 17px
    color #fff
    font-weight 600
    font-size 14px
    text-decoration none
    cursor pointer

  .close
    width 24px
    height 24px
    border solid 1px rgba(255, 255, 255, 0.3)
    border-radius: 20px
    color #fff
    font-size 14px
    background-color transparent
    cursor pointer
</style>
