<template>
  <div class="container" :style="{width: size, height: size}" @click="$emit('click')">
    <caret class="caret" :style="{transform: `rotate(${rotate})`}"></caret>
  </div>
</template>

<script>
import Caret from './caret'
export default {
  name: 'caret-round',
  components: {
    Caret
  },
  props: {
    size: {
      type: String,
      default: '24px'
    },
    rotate: {
      type: String,
      default: '0'
    }
  }
}
</script>

<style scoped lang="stylus">
.container
  border-radius 17px
  background $white
  box-shadow 0 2px 6px 0 rgba(0, 0, 0, 0.15)
  center()
</style>
