<template>
  <div class="uc-image-picker--multi-select">
    <!-- Selected items -->
    <div
      class="uc-image-picker--multi-select__slider"
    >
      <slider
        :items="selectedNodes"
        :container-width="sliderContext.containerWidth"
        :item-size="sliderContext.itemSize"
        :item-margin="sliderContext.itemMargin"
      >
        <template #content="{ item }">
          <div
            class="slider-item"
            :style="{
              width: `${sliderContext.itemSize}px`,
              height: `${sliderContext.itemSize}px`,
              'margin-right': `${sliderContext.itemMargin}px`
            }"
            @click="selectImage(item)"
          >
            <div class="slider-image">
              <uc-image
                :src="item.url"
                :alt="item.name"
                fit="scaleDown"
              />
            </div>
            <div class="deselect-slider-item">
              <uc-button-round
                theme="white"
                size="s"
                floating
              >
                <remove-icon />
              </uc-button-round>
            </div>
          </div>
        </template>
      </slider>
    </div>

    <!-- Empty Root State -->
    <div
      v-if="isEmptyRootState"
      class="uc-image-picker--multi-select__empty-root-state"
    >
      <uc-image-picker-empty-root-state :cms-host-name="hostSystemName" />
    </div>

    <!-- Image Picker -->
    <div
      v-else
      class="uc-image-picker--multi-select__picker"
    >
      <uc-image-picker
        :nodes="data"
        :is-loading="isLoading"
        :is-empty-root-state="isEmptyRootState"
        :selected-nodes="selectedNodesGuids"
        @getData="getData"
        @imageSelected="selectImage"
      >
        <template #actions>
          <uc-button
            v-if="imagesInFolder.length > 0"
            class="image-picker__select-all"
            containing-icon
            outlined
            :text="isEverythingInFolderSelected ? unselectAllLabel : selectAllLabel"
            data-test="ucImagePickerSelectAllButton"
            @click="selectAllClick"
          >
            <template #after>
              <uc-icon-with-border
                :icon="selectAllButtonIcon"
              />
            </template>
          </uc-button>
        </template>
      </uc-image-picker>
    </div>
    <div class="uc-image-picker__submit">
      <uc-button
        class="add-media-button"
        :text="confirmButtonLabel"
        :disabled="!selectedNodes.length > 0"
        primary
        big
        floating
        data-test="ucImagePickerAddMediaButton"
        @click="submitSelection"
      />
    </div>
  </div>
</template>

<script>

import UcImagePicker from '@/shared/components/generic/uc-image-picker/uc-image-picker'
import UcImagePickerHttp from '@/shared/components/editors/uc-image-picker-editor/uc-image-picker/uc-image-picker.http'
import intersection from 'lodash.intersection'
import { mapGetters } from 'vuex'
import UcButton from '@/shared/components/forms/uc-button/uc-button'
import { Close16Normal as RemoveIcon } from '@/shared/icons'
import UcIconWithBorder from '@/shared/components/widgets/uc-icon-with-border'
import { toggleObjectInArray, addObjectToArray, removeObjectFromArray } from '@/shared/components/generic/uc-image-picker/uc-image-picker.utils'
import Slider from '@/shared/components/widgets/slider'
import UcImage from '@/shared/components/generic/uc-image'
import UcButtonRound from '@/shared/components/forms/uc-button-round/uc-button-round'
import UcImagePickerEmptyRootState
  from '@/shared/components/editors/uc-image-picker-editor/uc-image-picker/components/uc-image-picker-empty-root-state'

export default {
  name: 'ImagePickerMultiSelect',
  components: {
    UcImagePickerEmptyRootState,
    UcButtonRound,
    UcImage,
    Slider,
    UcButton,
    UcImagePicker,
    UcIconWithBorder,
    RemoveIcon
  },
  props: {
    definition: {
      type: Object,
      required: true
    },
    data: {
      type: Array,
      required: true
    },
    getData: {
      type: Function,
      required: true
    },
    isLoading: {
      type: Boolean,
      default: () => false
    },
    isEmptyRootState: {
      type: Boolean,
      default: () => false
    },
    contentType: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      selectedNodes: [],
      sliderContext: {
        containerWidth: 800,
        itemSize: 50,
        itemMargin: 16
      }
    }
  },
  computed: {
    ...mapGetters(['systemInfo']),
    hostSystemName () {
      return this.systemInfo.cms
    },
    selectedNodesGuids () {
      return this.selectedNodes.map(image => image.guid)
    },
    imagesInFolder () {
      return this.data.filter(node => node.nodeType === 'Image')
    },
    imagesGuidsInFolder () {
      return this.imagesInFolder.map(node => node.guid)
    },
    isEverythingInFolderSelected () {
      return intersection(this.imagesGuidsInFolder, this.selectedNodesGuids).length === this.imagesGuidsInFolder.length
    },
    selectAllLabel () {
      return this.$t('global.selectAll')
    },
    unselectAllLabel () {
      return this.$t('global.unselectAll')
    },
    selectAllButtonIcon () {
      return this.isEverythingInFolderSelected ? 'Dual16Normal' : 'Add16Normal'
    },
    confirmButtonLabel () {
      return `${this.$t('global.done')} (${this.selectedNodes.length})`
    },
    isRequired () {
      return this.definition.isRequired
    }
  },
  async mounted () {
    const result = await UcImagePickerHttp.getSelectedMediaByGuids(this.contentType, this.definition.selected)
    this.selectedNodes = result.data
  },
  methods: {
    onChange (saveCommand) {
      this.$emit('change', saveCommand)
    },
    addAllMissingItemsToArray () {
      this.imagesInFolder.forEach(elem => addObjectToArray(this.selectedNodes, elem))
    },
    removeAllItemsFromArray () {
      this.imagesInFolder.forEach(elem => removeObjectFromArray(this.selectedNodes, elem))
    },
    selectImage (image) {
      toggleObjectInArray(this.selectedNodes, image)
    },
    selectAllClick () {
      this.isEverythingInFolderSelected ? this.removeAllItemsFromArray() : this.addAllMissingItemsToArray()
    },
    submitSelection () {
      this.$emit('imagesSelected', this.selectedNodes)
    }
  }
}
</script>

<style lang="stylus" scoped>
.uc-image-picker--multi-select
  display flex
  flex-direction column
  height 100%

.image-picker__select-all
  primary-details-text-semibold()

.uc-image-picker__wrapper
  position relative

.uc-image-picker__submit
  position absolute
  bottom 40px
  left 50%
  z-index 1
  transform translateX(-50%)

.deselect-slider-item
  position absolute
  top 50%
  left 50%
  transform translate(-50%,-50%)
  visibility hidden

.slider-item
  position relative
  display inline-block
  cursor pointer

  &:hover .deselect-slider-item
    visibility visible

  .slider-image
    box(100%)
    border solid 1px $col_border

.uc-image-picker--multi-select__slider
  height 50px
  margin-bottom 16px
  padding 0 120px

.uc-image-picker--multi-select__picker
  flex 1 0 auto
  height 0
</style>
