<template>
  <uc-navigator-grid-branch-node
    :selected="selected"
    :node="node"
    @open="openFolder"
  >
    <folder-icon />
  </uc-navigator-grid-branch-node>
</template>

<script>
import FolderIcon from '@/shared/icons/legacy/folder-image'
import UcNavigatorGridBranchNode from '@/shared/components/generic/uc-navigator/components/nodes/uc-navigator-grid-branch-node'

const ComponentEvent = {
  OPEN: 'open',
  SELECT: 'select'
}

export default {
  name: 'FolderComponent',
  components: {
    UcNavigatorGridBranchNode,
    FolderIcon
  },

  props: {
    node: {
      type: Object,
      required: true
    },
    selected: {
      type: Boolean,
      default: false
    }
  },
  data  () {
    return {
      hovered: false
    }
  },
  methods: {
    openFolder (node) {
      this.$emit(ComponentEvent.OPEN, node)
    }
  }
}
</script>
