<template>
  <div
    class="uc-navigator-grid-branch-node"
    data-test="grid-branch-node"
    @click="onClick"
  >
    <div
      class="uc-navigator-grid-branch-node__container"
      :class="cssClassObject"
    >
      <slot />
      <div class="uc-navigator-grid-branch-node__meta">
        {{ childCountLabel }}
      </div>
    </div>
    <div
      v-truncate
      class="uc-navigator-grid-branch-node__name"
    >
      {{ node.name }}
    </div>
  </div>
</template>

<script>

const ComponentEvent = {
  OPEN: 'open',
  SELECT: 'select'
}
export default {
  name: 'UcNavigatorNode',
  props: {
    isSelected: {
      type: Boolean,
      default: false
    },
    node: {
      type: Object,
      required: true
    }
  },
  computed: {
    cssClassObject () {
      return {
        'is-selected': this.isSelected
      }
    },
    childCountLabel () {
      return this.$tc('contentNavigator.items', this.node.childrenCount)
    }
  },
  methods: {
    onClick () {
      this.$emit(ComponentEvent.OPEN, this.node)
    }
  }
}
</script>
<style lang="stylus" scoped>
.uc-navigator-grid-branch-node
  border: 1px solid transparent

.uc-navigator-grid-branch-node__container
  position relative
  display flex
  flex-direction column;
  align-items center
  justify-content center
  width 100%
  height 150px
  border solid 1px $col_border
  border-radius 4px
  cursor pointer

.uc-navigator-grid-branch-node__name
  secondary-petit-text()
  margin 0
  padding 5px 0 0 0
// .is-selected
.uc-navigator-grid-branch-node__meta
  secondary-petit-text()
  margin 0
  padding 5px 0 0 0

.uc-navigator-grid-branch-node__container.is-selected
  border-color $ucommerce-blue

.uc-navigator-grid-branch-node:hover
  .uc-navigator-grid-branch-node__container
    border-color $ucommerce-blue
</style>
