import VueI18n from 'vue-i18n'
import dashboardMessages from '@/dashboard/translations/_main'
import catalogMessages from '@/catalogs/translations/_main'
import Vue from 'vue'

// Temporary until we move files to shared /src folder
function createMessage (countryCode) {
  return {
    ...catalogMessages[countryCode],
    ...dashboardMessages[countryCode],
    global: {
      ...catalogMessages[countryCode].global,
      ...dashboardMessages[countryCode].global
    }
  }
}

Vue.use(VueI18n)
const i18n = new VueI18n({
  locale: 'en',
  fallbackLocale: 'en',
  messages: {
    en: createMessage('en'),
    da: createMessage('da'),
    sv: createMessage('sv'),
    de: createMessage('de')
  }
})

export default i18n
