<template>
  <section
    class="fields-section"
    :class="cssClassObject"
    :v-custom-scroll-bar="scrollable"
    @click="sectionClicked"
  >
    <h2
      v-if="heading"
      class="fields-section__heading"
    >
      {{ heading }}
    </h2>
    <slot />
  </section>
</template>

<script>
export default {
  props: {
    heading: {
      type: String,
      default: ''
    },
    scrollable: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    cssClassObject () {
      return {
        'fields-section--scrollable': this.scrollable
      }
    }
  },
  methods: {
    sectionClicked () {
      this.$emit('sectionClicked')
    }
  }
}
</script>

<style lang="stylus" scoped>
$space = 8px

.fields-section
  margin 0 auto 0 auto
  padding 4 * $space 3 * $space
  background-color $ucommerce-white
  box-shadow 0 2px 8px 0 rgba(0, 0, 0, 0.1)

.fields-section + .fields-section
  margin-top 2.5 * $space

.fields-section__heading
  margin 0 0 3 * $space
  primary-heading-2()
  user-select none

.fields-section--scrollable
  max-height 575px
  overflow-y auto
</style>
